export function generateDropdown(name, label, options, defaultValue = null, onChange = null) {
	const wrapper = document.createElement("div")
	wrapper.className = "relative block w-full"

	// Create the button trigger
	const button = document.createElement("button")
	button.type = "button"
	button.className = "relative block w-full rounded-lg py-2 pl-4 pr-8 text-left text-base text-zinc-950 dark:text-white border border-zinc-300 dark:border-zinc-700 bg-transparent dark:bg-zinc-800 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-zinc-500 hover:border-zinc-500 dark:hover:border-zinc-600 transition"
	button.setAttribute("aria-haspopup", "listbox")
	button.setAttribute("aria-expanded", "false")

	// Create the button content
	const buttonContent = document.createElement("div")
	buttonContent.className = "flex items-center"

	// Add the label text
	const labelSpan = document.createElement("span")
	labelSpan.className = "ml-2 truncate"
	labelSpan.textContent = defaultValue || options[0].label
	buttonContent.appendChild(labelSpan)

	// Add the dropdown arrow
	const arrowSpan = document.createElement("span")
	arrowSpan.className = "pointer-events-none absolute inset-y-0 right-0 flex items-center pr-2"
	arrowSpan.innerHTML = `
		<svg class="w-5 h-5 text-zinc-500 dark:text-zinc-400" viewBox="0 0 16 16" aria-hidden="true" fill="none">
			<path d="M5.75 10.75L8 13L10.25 10.75" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"></path>
			<path d="M10.25 5.25L8 3L5.75 5.25" stroke-width="1.5" stroke-linecap="round" stroke-linejoin="round"></path>
		</svg>
	`

	button.appendChild(buttonContent)
	button.appendChild(arrowSpan)

	// Create the dropdown list
	const dropdownList = document.createElement("div")
	dropdownList.className = "absolute left-0 mt-2 w-full bg-white dark:bg-zinc-800 border border-zinc-300 dark:border-zinc-700 rounded-lg shadow-lg z-10"
	dropdownList.style.display = "none"

	// Create the options list
	const ul = document.createElement("ul")
	ul.className = "text-sm text-zinc-900 dark:text-zinc-100"

	// Create hidden input to store the value
	const hiddenInput = document.createElement("input")
	hiddenInput.type = "hidden"
	hiddenInput.name = name
	hiddenInput.value = defaultValue || options[0].value

	// Add options to the list
	options.forEach(option => {
		const li = document.createElement("li")
		li.className = "flex items-center px-4 py-2 cursor-pointer hover:bg-zinc-300 dark:hover:bg-zinc-700 m-1 rounded-lg"
		if (option.value === hiddenInput.value) {
			li.classList.add("bg-zinc-500", "text-white", "dark:bg-zinc-600")
		}

		const optionText = document.createElement("span")
		optionText.textContent = option.label

		// Add checkmark for selected option
		const checkmark = document.createElement("span")
		checkmark.className = "ml-auto text-white"
		checkmark.innerHTML = "&#10003;"
		checkmark.style.display = option.value === hiddenInput.value ? "block" : "none"

		li.appendChild(optionText)
		li.appendChild(checkmark)

		li.addEventListener("click", () => {
			// Update hidden input and button text
			hiddenInput.value = option.value
			labelSpan.textContent = option.label

			// Update selected state
			ul.querySelectorAll("li").forEach(item => {
				item.classList.remove("bg-zinc-500", "text-white", "dark:bg-zinc-600")
				item.querySelector("span:last-child").style.display = "none"
			})
			li.classList.add("bg-zinc-500", "text-white", "dark:bg-zinc-600")
			checkmark.style.display = "block"

			// Close dropdown
			dropdownList.style.display = "none"
			button.setAttribute("aria-expanded", "false")

			// Trigger onChange callback
			if (onChange) {
				onChange(option.value)
			}
		})

		ul.appendChild(li)
	})

	dropdownList.appendChild(ul)
	wrapper.appendChild(button)
	wrapper.appendChild(dropdownList)
	wrapper.appendChild(hiddenInput)

	// Toggle dropdown on button click
	button.addEventListener("click", (e) => {
		e.preventDefault()
		const isExpanded = button.getAttribute("aria-expanded") === "true"
		dropdownList.style.display = isExpanded ? "none" : "block"
		button.setAttribute("aria-expanded", !isExpanded)
	})

	// Close dropdown when clicking outside
	document.addEventListener("click", (e) => {
		if (!wrapper.contains(e.target)) {
			dropdownList.style.display = "none"
			button.setAttribute("aria-expanded", "false")
		}
	})

	// If a label is provided, wrap everything in a labeled container
	if (label) {
		const labeledContainer = document.createElement("div")
		labeledContainer.className = "space-y-2"

		const labelElement = document.createElement("label")
		labelElement.className = "block text-sm font-medium text-zinc-900 dark:text-zinc-100"
		labelElement.textContent = label

		labeledContainer.appendChild(labelElement)
		labeledContainer.appendChild(wrapper)
		return labeledContainer
	}

	return wrapper
}
