// simulation.js

import Alpine from "alpinejs"

import { API } from "../api.js"
import { Component } from "../components.js"
import {
	SIMULATION_DEFAULT_TIME_GENERAL,
	SIMULATION_DEFAULT_TIME_SPECIFIC,
	SIMULATION_DEFAULT_NUMBER_QUESTIONS_GENERAL,
	SIMULATION_DEFAULT_NUMBER_QUESTIONS_SPECIFIC
} from "../environment.js"
import { showModalX } from "../modals.js"
import { loadComponent } from "../router.js"
import { getState, updateState } from "../states.js"
import { translations, userLanguage } from "../translations.js"
import { apiRequest, submitAnswers } from "../utils.js"
import { generateButton, showConfirmationDialog } from "./components.js"
import { backdrop } from "./navigation.js"

class Simulation extends Component{
	constructor() {
		super("simulation")
	}

	async initialize() {
		const { filters } = getState()
		// Redirect to filters page if no filter data is found
		if (!filters || Object.keys(filters).length === 0) {
			console.error("No filter data found, redirecting to simulation filters page.")
			loadComponent("/simulation/filters")
			return
		}

		// Fetch data based on filters
		const data = await this.fetchData(filters)
		// Generate the content based on the fetched data
		this.generateContent(data)

		// Hide navigation while in simulation (only for desktop)
		backdrop(true)

		this.setupBeforeUnloadHandler()
		this.setupNavigationHandlers()

		// FIXME: Filters are being cleaned because state does not handle persistent data yet
		updateState({ filters: {}, navigationBlocked: true })
	}

	async fetchData(filters) {
		const api = API.endpoints.QUESTIONS.SELF
		const schema = API.getSchemaForEndpoint("QUESTIONS.SELF")

		delete filters.simulationType
		delete filters.time
		const queryString = API.buildQueryString(schema, filters)
		if (!queryString) return // FIXME?: Error already handled in buildQueryString

		const { response, error } = await apiRequest(api + queryString, "GET", {})
		if (error) return // FIXME?: Error already handled in apiRequest

		return response.questions

	}

	setupNavigationHandlers() {
		// Add popstate event listener for back/forward buttons
		window.addEventListener("popstate", async (event) => {
			if (getState().navigationBlocked) {
				const confirmed = await showConfirmationDialog(
					"Si abandonas la página perderás tu progreso. ¿Deseas salir?",
					"Abandonar",
					"Cancelar"
				)

				if (!confirmed) {
					return // do nothing
				}

				// Allow navigation and cleanup
				updateState({ navigationBlocked: false })
				backdrop(false)
				window.removeEventListener("beforeunload", this.beforeUnloadHandler)
				window.location.reload()
			}
		})
	}

	setupBeforeUnloadHandler() {
		const handler = async (event) => {
			event.preventDefault()
		}

		window.addEventListener("beforeunload", handler)
		this.beforeUnloadHandler = handler
	}

	generateContent(questions) {
		const form = document.querySelector("#simulationForm")
		form.innerHTML = ""

		// Generate questions content
		questions.forEach((question, index) => {
			const questionElement = this.createQuestionElement(question, index)
			form.appendChild(questionElement)
		})

		// Add the submit button
		const submitButton = generateButton("submitBtn", "Enviar respuestas", "submit", "primary")
		form.appendChild(submitButton)

		// Enable submit button only when all questions are answered
		// enableSubmitButtonOnAllAnswered(form, submitButton, questions.length)

		form.addEventListener("submit", (event) => this.handleFormSubmit(event, form, questions))
		// form.addEventListener("htmx:beforeRequest", (e) => this.handleSubmit(e, form, questions))
	}

	createQuestionElement(question, index) {
		const questionDiv = document.createElement("div")
		questionDiv.className = "space-y-4 p-4 border border-zinc-300 dark:border-zinc-700 rounded-lg mb-6 bg-white dark:bg-zinc-800"

		// Question text
		const questionText = document.createElement("p")
		questionText.className = "text-lg font-semibold text-zinc-900 dark:text-zinc-100 mb-4"
		questionText.innerText = `${index + 1}. ${question.question}`
		questionDiv.appendChild(questionText)

		// Answer options
		const answers = JSON.parse(question.answers)
		const radioGroup = document.createElement("div")
		radioGroup.className = "space-y-3"

		answers.forEach((answer, key) => {
			const label = document.createElement("label")
			label.className = "flex items-center space-x-3 cursor-pointer"

			const radioButton = document.createElement("input")
			radioButton.type = "radio"
			radioButton.name = `question-${question.id}`
			radioButton.value = key
			radioButton.className = "sr-only"

			const customRadio = document.createElement("span")
			customRadio.className = "flex flex-shrink-0 h-5 w-5 items-center justify-center border-2 border-zinc-400 rounded-full dark:border-zinc-600 transition-colors"

			const innerDot = document.createElement("span")
			innerDot.className = "h-2 w-2 rounded-full bg-white transition-opacity opacity-0"
			customRadio.appendChild(innerDot)

			// Handle clicks on the label
			label.addEventListener("click", (e) => {
				e.preventDefault() // Prevent default radio behavior

				if (radioButton.checked) {
					// Deselect if already selected
					radioButton.checked = false
					customRadio.classList.remove("bg-zinc-900", "dark:bg-zinc-600", "border-transparent")
					innerDot.classList.add("opacity-0")
				} else {
					// Select this option and deselect others
					radioGroup.querySelectorAll("input[type='radio']").forEach(input => {
						const customRadio = input.parentElement.querySelector(".flex.h-5.w-5")
						const innerDot = customRadio.querySelector(".h-2.w-2")

						input.checked = (input === radioButton)
						if (input.checked) {
							customRadio.classList.add("bg-zinc-900", "dark:bg-zinc-600", "border-transparent")
							innerDot.classList.remove("opacity-0")
						} else {
							customRadio.classList.remove("bg-zinc-900", "dark:bg-zinc-600", "border-transparent")
							innerDot.classList.add("opacity-0")
						}
					})
				}
			})

			const answerText = document.createElement("span")
			answerText.className = "text-sm text-zinc-900 dark:text-zinc-100"
			answerText.innerText = `${String.fromCharCode(65 + key)}. ${answer}`

			label.appendChild(radioButton)
			label.appendChild(customRadio)
			label.appendChild(answerText)
			radioGroup.appendChild(label)
		})

		questionDiv.appendChild(radioGroup)
		return questionDiv
	}

	async handleFormSubmit(event, form, questions) {
		event.preventDefault()

		const { timeUp } = getState()
		if (!timeUp) {
			const userConfirmed = await showConfirmationDialog(translations[userLanguage].submitConfirmation)
			if (!userConfirmed) return
			updateState({ timeUp: false })
		}

		updateState({ navigationBlocked: false })
		// Collect answers and update questions
		const formData = new FormData(form)
		this.updateSelectedAnswers(formData, questions)

		try {
			const { error } = await submitAnswers(questions, "simulation")
			if (error) {
				showModalX("error", "Error enviando respuestas", error)
				return
			}

			backdrop(false)

			updateState({ data: questions })
			loadComponent("/simulation/results")

		} catch (error) {
			showModalX("error", "Error enviando simulación", error)
			return
		}
	}

	updateSelectedAnswers(formData, questions) {
		formData.forEach((value, key) => {
			const questionId = key.split("-")[1]
			const question = questions.find(q => q.id === Number(questionId))
			if (question) question.selectedAnswer = Number(value)
		})
		return questions
	}
}

document.addEventListener("alpine:init", () => {
	Alpine.data("countdownTimer", () => ({
		initialTime:   null, // Will be set in init based on simulation type
		size:          60, // Fixed size for the container
		radius:        27, // Fixed radius for both circles
		strokeWidth:   5, // Stroke width of the circles
		timer:         null,

		// Computed property to calculate the circumference
		get circumference() {
			return 2 * Math.PI * this.radius
		},

		// Formatted time as MM:SS
		get formattedTime() {
			const mins = Math.floor(this.timeRemaining / 60)
			const secs = this.timeRemaining % 60
			return `${String(mins).padStart(2, "0")}:${String(secs).padStart(2, "0")}`
		},

		// Function to start the countdown
		startCountdown() {
			// Set end time if not exists
			const endTime = Date.now() + (this.initialTime * 60 * 1000)
			localStorage.setItem("simulationEndTime", endTime)

			// Clear any existing interval
			if (this.timer) clearInterval(this.timer)

			// Update time every second
			this.timer = setInterval(() => {
				const endTime = parseInt(localStorage.getItem("simulationEndTime"))
				const now = Date.now()
				const remaining = Math.max(0, Math.floor((endTime - now) / 1000))

				if (remaining <= 0) {
					clearInterval(this.timer)
					localStorage.removeItem("simulationEndTime")
					updateState({ timeUp: true })

					const form = document.querySelector("#simulationForm")
					if (form) form.requestSubmit()
				} else {
					this.timeRemaining = remaining
				}
			}, 1000)
		},

		// Initialization function
		init() {
			const { filters } = getState()
			const simulationType = filters?.simulationType || "general"
			this.initialTime = simulationType === "general" ? SIMULATION_DEFAULT_TIME_GENERAL : SIMULATION_DEFAULT_TIME_SPECIFIC
			this.timeRemaining = this.initialTime * 60
			updateState({ timeUp: false })
			this.startCountdown()
		}
	}))
})

// Export and initialize the Simulation component
export const simulationComponent = new Simulation("simulation")
export const initializeSimulation = () => simulationComponent.initialize()
