// errors.js (web)

import { translations, userLanguage } from "./translations"
import { extractSerializableResponse, safeStringify } from "./utils"
import { DEBUG } from "./environment"

// Centralized default error messages
const DEFAULT_MESSAGES = {
	UNKNOWN_ERROR_CODE:    translations[userLanguage].unknownErrorCode || "Unknown Error Code",
	UNKNOWN_ERROR_MESSAGE: translations[userLanguage].unknownErrorMessage || "An unknown error occurred",
	RESPONSE_MAP_ERROR:    translations[userLanguage].responseMapError || "Failed to map server response to error",
}

// Custom error class for web errors
export class WebError extends Error{
	constructor(originalError, code, message, context = {}, details = {}) {
		super()
		this.originalError = originalError
		this.name = "WebError"
		this.message = message || DEFAULT_MESSAGES.UNKNOWN_ERROR_MESSAGE
		this.code = code || DEFAULT_MESSAGES.UNKNOWN_ERROR_CODE
		this.level = "error"
		this.stack = new Error().stack

		// Optional fields
		if (Object.keys(context).length > 0) this.context = context
		if (Object.keys(details).length > 0) this.details = details
	}

	toJSON() {
		const { message, name, code, level, stack, originalError, context, details } = this
		return {
			level,
			name,
			code,
			message,
			stack,
			originalError,
			...(context && { context }),
			...(details && { details })
		}
	}
}

// Function to handle errors in server responses
export async function mapServerErrorResponse(response) {
	try {
		const body = await response.json()
		const serverError = body.error || {}

		const code = serverError.code || DEFAULT_MESSAGES.UNKNOWN_ERROR_CODE
		const message = serverError.message || DEFAULT_MESSAGES.UNKNOWN_ERROR_MESSAGE
		const details = serverError.details ? serverError.details : "No additional details"

		return new WebError(null, code, message, extractSerializableResponse(response), details)
	} catch (error) {
		const errorMessage = error.message || DEFAULT_MESSAGES.RESPONSE_MAP_ERROR
		// TODO: this is not a web error, it's a parsing error (server error?)
		return new WebError(error, "PARSE_ERROR", errorMessage, extractSerializableResponse(response), "No additional details")
	}
}

// Function to log events with robust serialization
export async function logEvent(level, message, context = null, details = null, extra = null) {
	try {
		const validLevel = typeof level === "string" ? level.toUpperCase() : "ERROR" // Ensure level is a string and uppercase it, default to "ERROR"
		const detailsStr = details ? safeStringify(details) : "No details available"
		const contextStr = context ? safeStringify(context) : "No context available"
		const extraStr = extra ? safeStringify(extra) : "No extra information"

		if (DEBUG) {
			const errorTag = `[${validLevel}_DEBUG]`
			console.log(`${errorTag} Message: ${message}`)
			if (details) console.log(`${errorTag} Details: ${detailsStr}`)
			if (context) console.log(`${errorTag} Context: ${contextStr}`)
			if (extra) console.log(`${errorTag} Extra: ${extraStr}`)
		}
	} catch (error) {
		console.error("Failed to log event:", error)
	}
}

// Function to handle and log web errors
export function handleWebError(error, extra = {}) {
	try {
		if (!(error instanceof WebError)) {
			error = new WebError(error, "UNKNOWN_ERROR", error.message || DEFAULT_MESSAGES.UNKNOWN_ERROR_MESSAGE)
		}
		logEvent(error.level, error.message, { originalError: safeStringify(error) }, error.details, extra)
		return {
			code:       error.code || DEFAULT_MESSAGES.UNKNOWN_ERROR_CODE,
			message:    error.message || DEFAULT_MESSAGES.UNKNOWN_ERROR_MESSAGE,
			statusCode: error.statusCode || 500,
		}
	} catch (err) {
		// Log the error that occurred while trying to handle the original error
		logEvent("ERROR", "Failed to handle error", { originalError: safeStringify(error), handlingError: safeStringify(err) }, error.details, extra)
		return {
			code:       DEFAULT_MESSAGES.UNKNOWN_ERROR_CODE,
			message:    DEFAULT_MESSAGES.UNKNOWN_ERROR_MESSAGE,
			statusCode: 500,
		}
	}
}
