// index.js
// This is the main entry point of the client-side code.

import htmx from "htmx.org"
import Alpine from "alpinejs"

// import { login, register } from "./auth.js"
import { loadComponent } from "./router.js"
import { getState, updateState } from "./states.js"
import { translations, userLanguage } from "./translations.js"
import { initializeNavigation } from "./components/navigation.js"
import { session } from "./session.js"
import { showModalX } from "./modals.js"
import * as utils from "./components/utils.js"


async function initializeApp() {
	try {
		await session.setupAutoRefresh()
		initializeNavigation()

		const initialState = getState()
		updateState({ ...initialState, currentRoute: window.location.pathname })

		// Load initial route
		loadComponent(window.location.pathname)

		utils.setTheme(localStorage.theme || utils.getSystemTheme())
		Alpine.start()
	}
	catch (error) {
		console.error("App initialization failed:", error)
		throw error
	}
}

htmx.defineExtension("json-encoding", {
	onEvent: function (name, evt) {
		if (name === "htmx:configRequest") {
			evt.detail.headers["Content-Type"] = "application/json"
			evt.detail.headers["Accept"] = "application/json"
		}
	},
	encodeParameters: function(xhr, parameters, elt) {
		// Convert parameters to JSON format
		return JSON.stringify(parameters)
	}
})

// Wait for DOM to be ready, then initialize the app
// document.addEventListener("DOMContentLoaded", initializeApp)
document.addEventListener("DOMContentLoaded", () => {
	initializeApp().catch(error => {
		console.error("Failed to initialize app:", error)
		showModalX("error", "Error de servidor", error)
	})
})

