// modals.js

import { translations, userLanguage } from "./translations"

/**
 * Function to display a customizable modal with dynamic buttons and content.
 *
 * @param {string} message - The content or body text to display in the modal.
 * @param {Array<Object>} [buttons=[]] - An optional array of button objects to dynamically create buttons in the modal. Each button object can define either a URL for redirection or a custom `onClick` function.
 * @param {Object} [extra={}] - An optional object for additional modal configurations such as title and close button text.
 * @param {string} [extra.title] - The title of the modal. Defaults to a generic title based on the user's language if not provided.
 *
 * @example
 * // Example 1: Modal with two buttons: one redirects, one logs an action
 * showModal(
 *    "Are you sure you want to proceed?",
 *    [
 *        {
 *            name: "Yes",
 *            url: "/confirm"  // Redirects to the confirmation page when clicked
 *        },
 *        {
 *            name: "No",
 *            onClick: () => {
 *                console.log("Action canceled");
 *            }
 *        }
 *    ],
 *    {
 *        title: "Confirmation"
 *    }
 * );
 *
 * @example
 * // Example 2: Modal with only a close button and a custom title
 * showModal(
 *    "This is a simple modal with just a close button.",
 *    [],
 *    {
 *        title: "Information",
 *    }
 * );
 *
 * @param {Function} showModal - Function to display a modal dialog with dynamic content and actions.
 */
export function showModal(message, buttons = [], extra = {}) {
	const modal = document.querySelector("#alert-modal")
	const modalTitle = document.querySelector("#alert-modal-title")
	const modalMessage = document.querySelector("#alert-modal-message")
	const modalButtonsContainer = document.querySelector("#alert-modal-buttons")
	const closeModalButton = document.querySelector("#alert-modal-close")

	// Set modal content
	modalTitle.textContent = extra.title || translations[userLanguage].modalInformation
	modalMessage.textContent = message

	// Clear any existing buttons
	modalButtonsContainer.innerHTML = ""

	// Logic for buttons: If none are given, default to close button
	if (!buttons.length) {
		closeModalButton.textContent = translations[userLanguage]?.modalButton
		closeModalButton.style.display = "inline-block" // Ensure close button is visible

		// Close modal on close button click
		closeModalButton.onclick = () => {
			modal.classList.remove("active")
		}

	} else {
		// Hide the close button when other buttons are provided
		closeModalButton.style.display = "none"

		// If buttons exist, create dynamic buttons
		buttons.forEach(button => {
			const btn = document.createElement("button")
			btn.textContent = button.name || "Button"
			btn.onclick = () => {
				if (button.url) {
					window.location.href = button.url // FIXME: Redirect if URL is provided
				} else if (button.onClick && typeof button.onClick === "function") {
					button.onClick() // Call custom onClick function if provided
				}
			}
			modalButtonsContainer.appendChild(btn)
		})
	}

	// Show modal by adding the 'active' class
	modal.classList.remove("hidden")
	modal.classList.add("active")

	// Optionally, close modal by clicking outside the content area
	modal.onclick = (event) => {
		if (event.target === modal) {
			modal.classList.remove("active")
			modal.classList.add("hidden")
		}
	}
}

/**
 * Function to show an error modal
 * @param {Error} error - The error object
 */
export function showErrorModal(error, extra = {}) {
	const errorMsg = `[ERROR]: ${error.code}: ${error.message} ${
		error.details ? `${JSON.stringify(error.details)}` : ""
	}`
	alert(errorMsg)
	console.log(`${errorMsg}${extra ? ` (${JSON.stringify(extra)})` : ""}`)
}


export function showModalXA(title = "Notification", message = "This is a notification.") {
	// Check if Alpine.js is loaded
	if (typeof Alpine === "undefined") {
		console.error("Alpine.js is required for this function to work.")
		return
	}

	// Create the modal container
	const modal = document.createElement("div")
	modal.setAttribute("role", "alert")
	modal.className = "fixed top-4 right-4 sm:top-4 sm:right-4 lg:top-8 lg:right-8 p-4 rounded-xl bg-zinc-500/75 text-white shadow-lg w-80 max-w-full z-50"

	// Set up Alpine.js properties and attributes
	modal.setAttribute("x-data", `{ show: true, title: '${title}', message: '${message}' }`)
	modal.setAttribute("x-show", "show")
	modal.setAttribute("x-transition:enter", "transition ease-out duration-300 transform")
	modal.setAttribute("x-transition:enter-start", "opacity-0 translate-y-4 sm:translate-y-0 sm:translate-x-4")
	modal.setAttribute("x-transition:enter-end", "opacity-100 translate-y-0 sm:translate-x-0")
	modal.setAttribute("x-transition:leave", "transition ease-in duration-300 transform")
	modal.setAttribute("x-transition:leave-start", "opacity-100 translate-y-0 sm:translate-x-0")
	modal.setAttribute("x-transition:leave-end", "opacity-0 translate-y-4 sm:translate-y-0 sm:translate-x-4")

	// Modal content
	modal.innerHTML = `
        <div class="flex items-start">
            <span class="text-green-500">
                <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor" class="w-6 h-6">
                    <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M9 12.75L11.25 15 15 9.75M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
                </svg>
            </span>

            <div class="ml-3 flex-1">
                <strong class="block text-lg font-medium text-white" x-text="title"></strong>
                <p class="text-sm text-zinc-300 mt-1" x-text="message"></p>
            </div>

            <button @click="show = false" class="text-zinc-500 hover:text-zinc-400 ml-3">
                <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor" class="w-5 h-5">
                    <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M6 18L18 6M6 6l12 12" />
                </svg>
            </button>
        </div>
    `

	// Append the modal to the body
	document.body.appendChild(modal)

	// Automatically hide the modal after 5 seconds using
	let timeout = setTimeout(() => {
		modal.remove()
	}, 5000)

	// If the user interacts with the modal, it will not be closed automatically
	modal.onmouseover = () => {
		clearTimeout(timeout)
	}

	// But it will be closed if the user hovers over it
	modal.onmouseleave = () => {
		timeout = setTimeout(() => {
			modal.remove()
		}, 5000)
	}
}

