export function showModalX(type = "success", title = "Notification", message = "This is a notification.") {
	// Ensure a container exists for stacking notifications
	let container = document.querySelector("#notification-container")
	if (!container) {
		container = document.createElement("div")
		container.id = "notification-container"
		container.className = `
            fixed top-4 right-4 sm:top-4 sm:right-4 lg:top-8 lg:right-8
            flex flex-col space-y-4 z-50
        `
		document.body.appendChild(container)
	}

	// Create the modal container
	const modal = document.createElement("div")
	modal.setAttribute("role", "alert")
	modal.className = `
        p-4 rounded-xl bg-zinc-500/75 text-white shadow-lg w-80 max-w-full z-50
        transform translate-x-full opacity-0
        transition-all duration-300 ease-in-out
    `

	// Modal content
	modal.innerHTML = `
        <div class="flex items-start">
            ${types[type]}
            <div class="ml-3 flex-1">
                <strong class="block text-lg font-medium text-white">${title}</strong>
                <p class="text-sm text-zinc-300 mt-1">${message}</p>
            </div>

            <button class="text-zinc-500 hover:text-zinc-400 ml-3" onclick="this.closest('div[role=alert]').remove()">
                <svg xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor" class="w-5 h-5">
                    <path stroke-linecap="round" stroke-linejoin="round" stroke-width="2" d="M6 18L18 6M6 6l12 12" />
                </svg>
            </button>
        </div>
    `

	// Prepend the notification to the container for top-to-bottom stacking
	container.prepend(modal)

	// Trigger animations (fade-in and slide-in)
	requestAnimationFrame(() => {
		modal.classList.remove("translate-x-full", "opacity-0")
		modal.classList.add("translate-x-0", "opacity-100")
	})

	// Automatically hide the modal after 5 seconds
	let timeout = setTimeout(() => hideModal(), 5000)

	// Clear the timeout when hovering over the modal
	modal.onmouseover = () => clearTimeout(timeout)

	// Restart the timeout when the mouse leaves the modal
	modal.onmouseleave = () => {
		timeout = setTimeout(() => hideModal(), 5000)
	}

	// Hide the modal with fade-out and slide-out effect
	function hideModal() {
		modal.classList.remove("translate-x-0", "opacity-100")
		modal.classList.add("translate-x-full", "opacity-0")

		// Remove from DOM after transition completes
		modal.addEventListener("transitionend", () => {
			modal.remove()
			// Clean up the container if empty
			if (!container.children.length) {
				container.remove()
			}
		}, { once: true })
	}
}
