// api.js

import { SERVER_URL } from "./environment.js"
import { mapServerErrorResponse, WebError } from "./errors.js"
import { showErrorModal, showModalX } from "./modals.js"

/**
 * @file api.js
 * @description Factory pattern implementation for API contracts, schemas and endpoint validation
 */

/**
 * SchemaBuilder - Creates schema definitions for API parameters
 * @class
 * @example
 * const schema = new SchemaBuilder();
 * const numberField = schema.number(10); // {type: "number", default: 10}
 * const arrayField = schema.array("number"); // {type: "array", itemType: "number"}
 */
class SchemaBuilder{
	/**
     * Creates a number field schema
     * @param {number} [defaultValue] - Optional default value
     * @returns {Object} Schema definition
     */
	number(defaultValue = undefined) {
		return {
			type: "number",
			...(defaultValue !== undefined && { default: defaultValue })
		}
	}

	/**
     * Creates an array field schema
     * @param {string} itemType - Type of array items
     * @returns {Object} Schema definition
     */
	array(itemType) {
		return {
			type: "array",
			itemType
		}
	}

	/**
     * Creates a string field schema
     * @param {number} minLength
     * @param {number} maxLength
     * @returns {Object} Schema definition
     */
	string(minLength = 0, maxLength = Infinity) {
		return {
			type: "string",
			minLength,
			maxLength
		}
	}

	/**
     * Creates a date field schema
     * @param {string} format - Date format
     * @returns {Object} Schema definition
     * @example
     * const dateField = schema.date("ISO"); // {type: "date", format: "ISO"}
     * const dateField = schema.date("RFC3339"); // {type: "date", format: "RFC3339"}
     */
	date(format = "ISO") {
		return {
			type: "date",
			format
		}
	}

	/**
     * Creates an enum field schema
     * @param {Array} values - Enum values
     * @returns {Object} Schema definition
     * @example
     * const enumField = schema.enum(["red", "green", "blue"]); // {type: "enum", values: ["red", "green", "blue"]}
     */
	enum(values) {
		return {
			type: "enum",
			values
		}
	}

	/**
     * Creates an object field schema
     * @param {Object} properties - Object properties
     * @returns {Object} Schema definition
     * @example
     * const objectField = schema.object({name: schema.string(), age: schema.number()}); // {type: "object", properties: {name: {type: "string"}, age: {type: "number"}}}
     */
	object(properties) {
		return {
			type: "object",
			properties
		}
	}
}

/**
 * EndpointBuilder - Creates standardized API endpoint paths
 * @class
 * @example
 * const endpoints = new EndpointBuilder("v1");
 * endpoints.create("users"); // "/api/v1/users"
 */
class EndpointBuilder{
	constructor(version = "v1") {
		this.basePath = `/api/${version}`
	}

	create(path) {
		// return `${this.basePath}/${path}`
		return `/${path}`
	}
}

/**
 * APIContractFactory - Main factory for creating API contracts
 * @class
 * @example
 * const factory = new APIContractFactory();
 * const API = factory.createContract();
 *
 * // Use API contract
 * const endpoint = API.endpoints.QUESTIONS;
 * const queryString = API.buildQueryString(params, API.schemas.questionFilters);
 */
class APIContractFactory{
	constructor(version = "v1") {
		this.schema = new SchemaBuilder()
		this.endpoint = new EndpointBuilder(version)
	}

	createContract() {
		const validator = {
			validateParams:   this.validateParams,
			buildQueryString: this.buildQueryString
		}

		return {
			endpoints: {
				// Questions domain
				QUESTIONS: {
					BASE:             this.endpoint.create("questions/"),
					SELF:             this.endpoint.create("questions/self"),
					SUBMISSIONS:      this.endpoint.create("questions/submissions"),
					LAST_SUBMISSION:  this.endpoint.create("questions/submissions/last"),
					DISPUTES:         this.endpoint.create("questions/disputes"),
					SUBMIT:           this.endpoint.create("questions/submit"),
					BLACKLIST:        this.endpoint.create("questions/blacklist")
				},
				// Users domain
				USERS: {
					SELF:    this.endpoint.create("users/self/"),
					BASE:    this.endpoint.create("users"),
					PROFILE: this.endpoint.create("users/profile"),
					STATS:   this.endpoint.create("users/stats")
				},
				// Auth domain
				AUTH: {
					LOGIN:   this.endpoint.create("login"),
					LOGOUT:  this.endpoint.create("logout"),
					REFRESH: this.endpoint.create("refresh")
				}
			},
			schemas: {
				// Questions domain
				QUESTIONS: {
					BASE: {
						amount:   this.schema.number(10),
						yearFrom: this.schema.number(),
						yearTo:   this.schema.number(),
						regions:  this.schema.array("number")
					},
					SELF: {
						GET: {
							amount:   this.schema.number(10),
							yearFrom: this.schema.number(),
							yearTo:   this.schema.number(),
							regions:  this.schema.array("number"), // FIXME: Should be enum?
							topics:   this.schema.array("string")
						}
					},
					DISPUTES: {
						POST: {
							id:         this.schema.number(),
							reason:     this.schema.string(0, 500)
						},
						GET: {
							userId:      this.schema.number()
						},
					},
					BLACKLIST: {
						POST: {
							id: this.schema.number()
						}
					},
					SUBMISSIONS: {
						POST: {
							answers:        this.schema.array("object"),
							submissionType: this.schema.enum(["undefined"])
						},
						GET: {
							amount:         this.schema.number(5),
							// submissionType: this.schema.enum(["eir"]),
							startDate:      this.schema.date(),
							endDate:        this.schema.date()
						},
					},
				},
				// Users domain
				USERS: {
					// SELF:  {},
					STATS: {
						answers:        this.schema.array("object"),
						submissionType: this.schema.enum(["eir"])
					},
					PROFILE: {
						name:  this.schema.string(),
						email: this.schema.string()
					}
				},
				AUTH: {
					LOGIN: {
						POST: {
							email:    this.schema.string(10, 50),
							password: this.schema.string(10, 50)
						}
					},
					LOGOUT:  {POST: {}},
					REFRESH: {POST: {}},
				}
			},

			getSchemaForEndpoint(endpointPath, method = "GET") {
				const [domain, action] = endpointPath.split(".")
				const schemas = this.schemas || {}

				// Check if the schema exists for the given domain and action
				const endpointSchema = schemas[domain]?.[action]
				if (!endpointSchema) return null

				// Return the schema specific to the HTTP method
				return endpointSchema[method] || null
			},

			getSchemaForEndpoint2(endpointPath) {
				const [domain, action] = endpointPath.split(".")
				const schemas = this.schemas || {}
				return schemas[domain]?.[action] || schemas[domain]?.BASE
			},

			validateParams:   validator.validateParams,
			buildQueryString: validator.buildQueryString
		}
	}


	validateParams(schema, params = {}) {
		const errors = []
		const validated = {}

		Object.entries(params).forEach(([key, value]) => {
			const fieldSchema = schema[key]
			if (!fieldSchema) {
				errors.push(`Parámetro desconocido: ${key}`)
				return
			}

			if (fieldSchema.type === "number") {
				const num = Number(value)
				if (isNaN(num)) {
					errors.push(`${key} must be a number`)
				} else {
					validated[key] = num
				}
			}

			if (fieldSchema.type === "array") {
				if (Array.isArray(value)) {
					validated[key] = value.join(",")
				} else {
					errors.push(`${key} must be an array`)
				}
			}

			if (fieldSchema.type === "string") {
				if (typeof value === "string") {
					validated[key] = value
				}
			}

			if (fieldSchema.type === "date") {
				const date = new Date(value)
				if (isNaN(date)) {
					errors.push(`${key} must be a valid date`)
				} else {
					validated[key] = date
				}
			}

			if (fieldSchema.type === "enum") {
				if (fieldSchema.values.includes(value)) {
					validated[key] = value
				}
			}

			if (fieldSchema.type === "object") {
				const { validated: nested, errors: nestedErrors } = this.validateParams(fieldSchema.properties, value)
				validated[key] = nested
				errors.push(...nestedErrors)
			}
		})

		return { validated, errors }
	}

	buildQueryString(schema, params = {}) {
		const { validated, errors } = this.validateParams(schema, params)

		if (errors.length) {
			console.error("Validation errors:", errors)
			showModalX("error", "Validation failed", errors)
			return errors
		}

		const urlParams = new URLSearchParams()
		Object.entries(validated).forEach(([key, value]) => {
			if (value !== null) {
				// if (Array.isArray(value)) {
				// 	urlParams.append(key, value.join(","))
				// } else {
				urlParams.append(key, value.toString())
				// }
			}
		})

		return urlParams.toString() ? `?${urlParams}` : ""
	}
}

async function apiRequest(endpoint, method = "GET", options = {}) {
	try {
		// FIXME: Normalize endpoint URL until SERVER_URL is defined without the trailing slash
		if (endpoint.charAt(0) === "/") endpoint = endpoint.slice(1)

		const response = await fetch(SERVER_URL + endpoint, {
			method,
			headers:     { "Content-Type": "application/json" },
			credentials: "include",
			...options,
		})

		if (!response.ok) {
			const error = await mapServerErrorResponse(response)
			return { response: null, error }
		}

		let data = null
		// Only parse JSON if response has content
		if (![101, 204, 205, 304].includes(response.status)) {
			data = await response.json()
		}
		if (data.error) {
			return { response: null, error: data.error }
		}

		return { response: data, error: null }

	} catch (error) {
		console.error("Unknown error:", error)
		const detailedError = error instanceof TypeError && error.message.includes("fetch")
			? { type: "ERROR_DE_RED", message: "No se puede conectar al servidor. Por favor, verifique su conexión de red e inténtelo de nuevo." }
			: { type: "ERROR_DESCONOCIDO", message: "Ha ocurrido un error inesperado. Por favor, inténtelo de nuevo." }

		showModalX("error", detailedError.type, detailedError.message)
		return { response: null, error: detailedError }
	}
}

// New generalized function to handle API requests with schema
export async function fetchAPI(endpointKey, method = "GET", query = {}, body = null) {
	try {
		// Extract the endpoint and schema
		const endpointParts = endpointKey.split(".")

		let api
		switch (endpointParts.length) {
		case 1:
			api = API.endpoints[endpointParts[0]]
			break
		case 2:
			api = API.endpoints[endpointParts[0]][endpointParts[1]]
			break
		default:
			throw new Error("Invalid endpoint key")
		}

		if (!api) {
			throw new Error("Invalid endpoint key")
		}

		const schema = API.getSchemaForEndpoint(endpointKey)

		// Build the query string
		let uri = api
		if (method === "GET") {
			const queryString = API.buildQueryString(schema, query)
			uri += `${queryString?.length ? `${queryString}` : ""}`
		}
		// FIXME: For POST requests, add the id in the URL if provided in the query
		if (method === "POST" && query.id) {
			uri += `/${query.id}`
		}

		// Prepare fetch options
		const options = {
			method,
			...(body ? { body: JSON.stringify(body) } : {}),
		}

		// Make the API request using apiRequest
		const { response, error } = await apiRequest(uri, method, options)
		if (error) {
			console.error("Error during API request:", error)
			return { error }
		}
		return { response }
	} catch (error) {
		console.error("Unexpected error:", error)
		return { error }
	}
}


// Create and export API instance
const factory = new APIContractFactory()
export const API = factory.createContract()
