// Simulation constants
export const SIMULATION_DEFAULT_TIME_GENERAL = DEBUG ? 1 : 60 // Default time for general simulation in minutes
export const SIMULATION_DEFAULT_TIME_SPECIFIC = DEBUG ? 1 : 75 // Default time for specific simulation in minutes
export const SIMULATION_DEFAULT_NUMBER_QUESTIONS_GENERAL = 40 // Default number of questions for general simulation
export const SIMULATION_DEFAULT_NUMBER_QUESTIONS_SPECIFIC = 65 // Default number of questions for specific simulation

// Study constants
export const STUDY_DEFAULT_VALUE = 10 // Default number of questions for the study
export const STUDY_MIN_QUESTIONS = 5 // Minimum allowed number of questions for the study
export const STUDY_MAX_QUESTIONS = 25 // Maximum allowed number of questions for the study
export const DASHBOARD_TABLE_HISTORY_AMOUNT = 5 // Number of rows to display in the dashboard table
export const SUCCESS_THRESHOLD = 0.8 // 80% success threshold for the success/failure badges of the dashboard table

// | Unit    | Conversion Formula    | Value   |
// | ------- | --------------------- | ------- |
// | Seconds | 60         (1 minute) | 60      |
// | Minutes | 60×60      (1 hour)   | 3,600   |
// | Hours   | 60×60×24   (1 day)    | 86,400  |
// | Days    | 60×60×24×7 (1 week)   | 604,800 |
export const REFRESH_TOKEN_TIME_INTERVAL = 60 * 10 // 10 minutes (in seconds)
export const REFRESH_TOKEN_RENEW_CHECKPOINT = 60 * 60 * 6 // 6 hours (in seconds)
