
export function renderNavLogoSkeleton() {

	document.querySelectorAll("[data-brand-logo]").forEach(brandLogo => {
		brandLogo.classList.add("hidden") // not needed for skeleton
		// brandLogo.classList.add("animate-pulse", "bg-gray-200", "dark:bg-gray-700")
		brandLogo.src = "" // Empty source for skeleton
	})

	document.querySelectorAll("[data-brand-name]").forEach(brandName => {
		brandName.innerHTML = "<div class=\"h-4 bg-gray-200 dark:bg-gray-700 rounded w-24 animate-pulse\"></div>"
	})
}

export function renderNavSkeleton() {
	document.querySelectorAll("[data-nav-links]").forEach(navLinks => {
		const skeletonItems = Array(4).fill().map(() => `
            <span class="relative">
                <div class="flex w-full items-center gap-3 px-2 py-2.5">
                    <div class="size-6 rounded bg-gray-200 dark:bg-gray-700 animate-pulse"></div>
                    <div class="h-4 bg-gray-200 dark:bg-gray-700 rounded w-24 animate-pulse"></div>
                </div>
            </span>
        `).join("")
		navLinks.innerHTML = skeletonItems
	})
}

export function renderSupportSkeleton() {
	document.querySelectorAll("[data-support-links]").forEach(supportLinks => {
		supportLinks.innerHTML = `
            <div class="flex flex-col gap-0.5">
                <div class="flex items-center gap-3 px-2 py-2.5">
                    <div class="size-6 rounded bg-gray-200 dark:bg-gray-700 animate-pulse"></div>
                    <div class="h-4 bg-gray-200 dark:bg-gray-700 rounded w-20 animate-pulse"></div>
                </div>
            </div>
        `
	})
}

export function renderEventsSkeleton() {
	document.querySelectorAll("[data-latest-links]").forEach(latestLinks => {
		const skeletonEvents = Array(3).fill().map(() => `
            <div class="px-2 py-2.5">
                <div class="h-4 bg-gray-200 dark:bg-gray-700 rounded w-full animate-pulse mb-2"></div>
                <div class="h-3 bg-gray-200 dark:bg-gray-700 rounded w-3/4 animate-pulse"></div>
            </div>
        `).join("")
		latestLinks.innerHTML = skeletonEvents
	})
}

export function renderProfileSkeleton() {
	const avatar = document.querySelector("#user-profile-avatar")
	if (avatar) {
		avatar.classList.add("animate-pulse", "bg-gray-200", "dark:bg-gray-700")
		avatar.src = "https://www.svgrepo.com/show/496707/user-square.svg"
	}

	const name = document.querySelector("#user-profile-name")
	if (name) {
		name.innerHTML = "<div class=\"h-4 bg-gray-200 dark:bg-gray-700 rounded w-20 animate-pulse\"></div>"
	}
}
