// study.js

import Alpine from "alpinejs"

import { API } from "../api.js"
import { Component } from "../components.js"
import { showModalX } from "../modals.js"
import { loadComponent } from "../router.js"
import { getState, updateState } from "../states.js"
import { apiRequest, submitAnswers } from "../utils.js"
import { showConfirmationDialog } from "./components.js"
import { translations, userLanguage } from "../translations.js"

class Study extends Component{
	constructor() {
		super("study")
	}

	async initialize(paramfilters) {
		const { filters } = getState()

		if (!filters || Object.keys(filters).length === 0) {
			console.error("No filter data found, redirecting to study filters page.")
			loadComponent("/study/filters")
			return
		}

		// Reset the Alpine store before loading new questions
		this.resetStore()
		// Fetch the data based on filters and update the store
		await this.fetchAndStoreQuestions(filters)

		// Setup the beforeunload handler to prevent accidental page reload
		this.setupBeforeUnloadHandler()
		this.setupNavigationHandlers()

		// FIXME: Filters are being cleaned because state does not handle persistent data yet
		updateState({ filters: {}, navigationBlocked: true })
	}

	resetStore() {
		Alpine.store("studyData").reset()
	}

	async fetchData(filters) {
		delete filters.simulationType

		const api = API.endpoints.QUESTIONS.SELF
		const schema = API.getSchemaForEndpoint("QUESTIONS.SELF")

		const queryString = API.buildQueryString(schema, filters)
		if (!queryString) return // FIXME?: Error already handled in buildQueryString

		const { response, error } = await apiRequest(api + queryString, "GET", {})
		if (error) return // FIXME?: Error already handled in apiRequest

		return response.questions
	}

	async fetchAndStoreQuestions(filters) {
		try {
			const questions = await this.fetchData(filters)
			if (questions && questions.length > 0) {
				Alpine.store("studyData").setCards(transformDataToCards(questions))
				Alpine.store("studyData").questions = questions
			} else {
				console.error("No questions found for the given filters.")
				Alpine.store("studyData").setCards([]) // Clear cards if no questions found
			}
		} catch (error) {
			console.error("Error fetching questions:", error)
		}
	}

	hasUnsavedChanges() {
		const studyData = Alpine.store("studyData")
		// Check if there are any answered questions
		return Object.keys(studyData.answers).length > 0 && !studyData.submitted
	}

	setupBeforeUnloadHandler() {
		const handler = async (event) => {
			event.preventDefault()
		}

		window.addEventListener("beforeunload", handler)
		this.beforeUnloadHandler = handler
	}

	setupNavigationHandlers() {
			// Add popstate event listener for back/forward buttons
			window.addEventListener("popstate", async (event) => {
				if (getState().navigationBlocked) {
					const confirmed = await showConfirmationDialog(
						"Si abandonas la página perderás tu progreso. ¿Deseas salir?",
						"Abandonar",
						"Cancelar"
					)

					if (!confirmed) {
						return // do nothing
					}

					// Allow navigation and cleanup
					updateState({ navigationBlocked: false })
					backdrop(false)
					window.removeEventListener("beforeunload", this.beforeUnloadHandler)
					window.location.reload()
				}
			})
		}
}

// Transform function for questions data
function transformDataToCards(questions) {
	return questions.map((question, index) => ({
		id:            index + 1,
		title:         question.question,
		content:       JSON.parse(question.answers).map((answer, index) => `${String.fromCharCode(65 + index)}. ${answer}`),
		correctAnswer: question.accepted,
		explanation:   question.explanation,
	}))
}

function itemsStore() {
	return {
		cards:      [],
		answers:    {},
		questions:  [],
		activeCard: 1,

		autonext:   true,
		reset() {
			// Reset the store to its initial state
			this.cards = []
			this.activeCard = 1
			this.answers = {}
			this.questions = []
		},
		setCards(newCards) {
			this.cards = newCards
		},
		selectAnswer(cardId, answerIndex, autonext = false) {
			if (this.answers[cardId] === undefined) {
				this.answers[cardId] = answerIndex
			}
			if (autonext && this.activeCard < this.cards.length) {
				this.activeCard++
			}
		},
		markAsBlank(cardId) {
			if (this.answers[cardId] === undefined) {
				this.answers[cardId] = null // blank response, no answer selected
			}
		},
		isAnswered(cardId) {
			return this.answers[cardId] !== undefined
		},
		async submitResults() {
			const confirmed = await showConfirmationDialog(translations[userLanguage].submitConfirmation)
			if (!confirmed) {
				return
			}

			if (!this.questions || !this.questions.length) {
				showModalX("error", "Error enviando respuestas", "No hay preguntas para enviar")
				return
			}

			// Create a deep copy to avoid modifying original data
			const questionsToSubmit = this.questions.map((question, index) => {
				const cardId = index + 1
				const selected = this.answers[cardId]

				const processedQuestion = {...question}
				if (selected !== null) processedQuestion.selectedAnswer = selected
				return processedQuestion
			})

			try {
				const { error } = await submitAnswers(questionsToSubmit, "study")
				if (error) {
					showModalX("error", "Error enviando respuestas", error)
					return
				}

				// const listeners = window.getEventListeners?.("beforeunload") || []
				// listeners.forEach(listener => {
				// 	window.removeEventListener("beforeunload", listener.listener)
				// })
				updateState({ data: questionsToSubmit, navigationBlocked: false })
				loadComponent("/study/results")

			} catch (error) {
				showModalX("error", "Error enviando estudio", error)
				return
			}
		},
		scrollToExplanation() {
            setTimeout(() => {
                const activeCard = document.querySelector(`[data-card-id="${this.activeCard}"]`)
				if (activeCard) activeCard.scrollTop = activeCard.scrollHeight
            }, 100); // Small delay to ensure content is rendered
        }
	}
}

// Register the store with Alpine.js
document.addEventListener("alpine:init", () => {
	Alpine.store("studyData", itemsStore())
})

export const studyComponent = new Study("study")
export const initializeStudy = (filters) => studyComponent.initialize(filters)
