// filters.js

import { updateState } from "../states.js"
import { loadComponent } from "../router.js"
import { Component } from "../components.js"
import {
	generateButton,
	generateSearchBox,
	generateQuantityField,
	generateSwitch,
	generateTable,
	createNavLink,
	generateDropdown
} from "./components.js"

import {
	SIMULATION_DEFAULT_TIME_GENERAL,
	SIMULATION_DEFAULT_TIME_SPECIFIC,
	SIMULATION_DEFAULT_NUMBER_QUESTIONS_GENERAL,
	SIMULATION_DEFAULT_NUMBER_QUESTIONS_SPECIFIC,
	STUDY_DEFAULT_VALUE,
	STUDY_MIN_QUESTIONS,
	STUDY_MAX_QUESTIONS
} from "../environment.js"

// TODO: Replace with actual topics from the database
const topicsGeneral = {
	"extra_parts:1_lesson:1": "Tema 1 General",
	"extra_parts:1_lesson:2": "Tema 6 General",
}

const topicsSpecific = {
	"extra_parts:2_lesson:1": "Tema 1 Específico",
	// "extra_parts_5": "Tema 3 Específico",
}

const filterConfigs = {
	study: {
		targetRoute: "/study/play",
		fields:      [
			{ type:    "dropdown", name:    "simulationType", label:   "Tipo de contenido", options: [
				{ value: "general", label: "Fase General" },
				{ value: "specific", label: "Fase Específica" }
			]},
			{ type: "table", name: "table", label: "", headerPosition: "firstColumn" , alignment: "left" , rows: [["Número de preguntas", "Personalizado"], ["Tiempo", "Indefinido"]]},
			{ type: "quantity", name: "amount", label: "Número de preguntas", placeholder: "Ej: 10", value: STUDY_DEFAULT_VALUE, min: STUDY_MIN_QUESTIONS, max: STUDY_MAX_QUESTIONS },
			{ type: "search", name: "topics", label: "Temas", options: topicsGeneral },
			// { type: "switch", name: "failed", label: "Incluir preguntas falladas anteriormente" },
			{ type: "button", name: "submit", label: "Iniciar Estudio", buttonType: "submit" },
		]
	},
	simulation: {
		targetRoute: "/simulation/play",
		fields:      [
			{ type:    "dropdown", name:    "simulationType", label:   "Tipo de simulación", options: [
				{ value: "general", label: "General" },
				{ value: "specific", label: "Específico" }
			]},
			{ type: "table", name: "table", label: "", headerPosition: "firstColumn" , alignment: "left" , rows: [["Número de preguntas", `${SIMULATION_DEFAULT_NUMBER_QUESTIONS_GENERAL}`], ["Tiempo", `${SIMULATION_DEFAULT_TIME_GENERAL} minutos`]]},
			{ type: "search", name: "topics", label: "Temas", options: topicsGeneral },
			{ type: "button", name: "submit", label: "Iniciar Simulación", buttonType: "submit" },
		]
	}
}

class Filters extends Component{
	constructor(type) {
		super("filters")
		this.type = type // e.g., "study" or "simulation"
	}

	initialize() {
		const config = filterConfigs[this.type]
		if (!config) {
			console.error(`No configuration found for filter type: ${this.type}`)
			return
		}

		const mainContent = document.querySelector("#main-content")
		if (!mainContent) throw new Error("Main content element not found")

		const formSection = document.querySelector("#filters-content")
		if (!formSection) throw new Error("Filters form not found")
		formSection.innerHTML = "" // Clear previous content if necessary

		const backlink = createNavLink("/", "Inicio")
		// FIXME: Could this be made more generic?
		backlink.onclick = (e) => {
			e.preventDefault()
			const path = backlink.getAttribute("href")
			loadComponent(path)
		}
		formSection.appendChild(backlink)

		// Create form header
		const title = this.type === "study" ? "Estudio" : "Simulacro"
		const subtitle = this.type === "study" ? "Modo de estudio, con una interfaz gamificada" : "Simulación de un examen oficial, con un tiempo definido y preguntas aleatorias"
		const header = generateFormHeader(title, subtitle)
		formSection.appendChild(header)

		const form = document.createElement("form")
		form.id = `${this.type}FiltersForm`
		form.className = "bg-white dark:bg-zinc-900 rounded-lg shadow p-6 space-y-6 max-w-md mx-auto"

		// Create form content based on configuration
		createFormContent(form, this.type, config.fields)
		formSection.appendChild(form)

		// Save target route to state for routing after submission
		this.targetRoute = config.targetRoute
	}
}

function generateFormHeader(title, description) {
	// Create the main header container
	const header = document.createElement("div")
	header.className = "flex flex-wrap items-center gap-6 justify-left p-6 space-y-6 max-w-md mx-auto"

	// Create the inner div container
	const innerContainer = document.createElement("div")

	// Create the title container with flex
	const titleContainer = document.createElement("div")
	titleContainer.className = "flex flex-wrap items-center gap-x-4 gap-y-2"

	// Create the title element
	const titleElement = document.createElement("h1")
	titleElement.className = "text-2xl/8 font-semibold text-zinc-950 sm:text-xl/8 dark:text-white"
	titleElement.textContent = title
	titleContainer.appendChild(titleElement)

	// Add the date and location container
	const dateLocation = document.createElement("div")
	dateLocation.className = "mt-2 text-sm/6 text-zinc-500"
	dateLocation.innerHTML = `${description}`

	// Append the title container and date location to the inner container
	innerContainer.appendChild(titleContainer)
	innerContainer.appendChild(dateLocation)

	// Append the inner container to the main header
	header.appendChild(innerContainer)

	return header
}

// Initialize filters with type parameter
export const initializeFilters = (type) => {
	const filtersComponent = new Filters(type)
	filtersComponent.initialize()
}

function updateTableAndSearch(form, value, type) {
	// Find the table and search elements within the form
	const tableElement = form.querySelector("dl")
	const searchContainer = form.querySelector(".overflow-y-auto")

	if (!tableElement || !searchContainer) {
		console.error("Required elements not found:", { tableElement, searchContainer })
		return
	}

	// Only update table values for simulation type
	if (type === "simulation") {
		const cells = tableElement.querySelectorAll("dd")
		if (value === "general") {
			cells[0].textContent = SIMULATION_DEFAULT_NUMBER_QUESTIONS_GENERAL
			cells[1].textContent = `${SIMULATION_DEFAULT_TIME_GENERAL} minutos`
		} else {
			cells[0].textContent = SIMULATION_DEFAULT_NUMBER_QUESTIONS_SPECIFIC
			cells[1].textContent = `${SIMULATION_DEFAULT_TIME_SPECIFIC} minutos`
		}
	}

	// Update search box for both types
	searchContainer.innerHTML = ""
	const topics = value === "general" ? topicsGeneral : topicsSpecific
	Object.entries(topics).forEach(([key, value]) => {
		const checkboxLabel = document.createElement("label")
		checkboxLabel.className = "flex items-center space-x-3 cursor-pointer mb-2"

		const checkbox = document.createElement("input")
		checkbox.type = "checkbox"
		checkbox.name = "topics"
		checkbox.value = key
		checkbox.className = "sr-only"

		const checkIndicator = document.createElement("span")
		checkIndicator.className = "flex h-5 w-5 items-center justify-center border-2 border-zinc-400 rounded-md dark:border-zinc-600 transition-colors"

		const checkMark = document.createElementNS("http://www.w3.org/2000/svg", "svg")
		checkMark.setAttribute("xmlns", "http://www.w3.org/2000/svg")
		checkMark.setAttribute("class", "h-3 w-3 text-white")
		checkMark.setAttribute("fill", "none")
		checkMark.setAttribute("viewBox", "0 0 24 24")
		checkMark.setAttribute("stroke", "currentColor")
		checkMark.setAttribute("stroke-width", "2")

		const path = document.createElementNS("http://www.w3.org/2000/svg", "path")
		path.setAttribute("stroke-linecap", "round")
		path.setAttribute("stroke-linejoin", "round")
		path.setAttribute("d", "M5 13l4 4L19 7")
		checkMark.appendChild(path)

		checkMark.style.display = "none"
		checkIndicator.appendChild(checkMark)

		checkbox.addEventListener("change", () => {
			if (checkbox.checked) {
				checkIndicator.classList.add("bg-zinc-900", "border-transparent", "dark:bg-zinc-600")
				checkMark.style.display = "block"
			} else {
				checkIndicator.classList.remove("bg-zinc-900", "border-transparent", "dark:bg-zinc-600")
				checkMark.style.display = "none"
			}
		})

		const optionLabel = document.createElement("span")
		optionLabel.className = "text-sm text-zinc-900 dark:text-zinc-100"
		optionLabel.textContent = value

		checkboxLabel.appendChild(checkbox)
		checkboxLabel.appendChild(checkIndicator)
		checkboxLabel.appendChild(optionLabel)
		searchContainer.appendChild(checkboxLabel)
	})
}

function createFormContent(form, type, fields) {
	let fieldElement

	fields.forEach((field) => {
		switch (field.type) {
		case "button":
			fieldElement = document.createElement("div")
			fieldElement.className = "flex justify-end mt-6"
			fieldElement.appendChild(
				generateButton(field.name, field.label, field.buttonType, "primary", field.classList)
			)
			break
		case "quantity":
			fieldElement = generateQuantityField(field.name, "", field.value, field.min, field.max)
			break
		case "switch":
			fieldElement = generateSwitch(field.name, field.label)
			break
		case "search":
			fieldElement = generateSearchBox("Buscar temas...", field.options, "w-full", "h-64", field.name)
			break
		case "dropdown":
			fieldElement = generateDropdown(field.name, field.label, field.options, null, (value) => {
				updateTableAndSearch(form, value, type)
			})
			break
		case "table":
			fieldElement = generateTable(field.headerPosition, field.alignment, field.rows)
			break
		default:
			// error
			console.error("Invalid field type:", field.type)
		}
		form.appendChild(fieldElement)
	})

	// Attach event listener for form submission after adding to the DOM
	form.addEventListener("submit", (event) => handleFormSubmit(event, type))
	return form
}

function handleFormSubmit(event, type) {
	event.preventDefault()

	// Collect form data
	const formData = new FormData(event.target)
	const params = {}

	// Get the field configuration for the current form type
	const fieldsConfig = filterConfigs[type]?.fields || []

	// Populate params from FormData
	fieldsConfig.forEach((field) => {
		switch (field.type) {
		case "switch":
			// Treat switches as booleans based on checkbox presence
			params[field.name] = formData.has(field.name)
			break

		case "quantity":
			// Convert quantity values to integers
			params[field.name] = parseInt(formData.get(field.name), 10)
			break

		case "search":
			// Convert search values to arrays
			if (formData.getAll(field.name).filter(Boolean).length > 0) {
				params[field.name] = formData.getAll(field.name).filter(Boolean)
			}
			break

		case "select":
			params[field.name] = formData.get(field.name)
			break

		default:
			// Handle other field types if they exist in form data
			if (formData.has(field.name)) {
				params[field.name] = formData.get(field.name)
			}
			break
		}
	})

	// Set default values based on simulation type
	if (type === "simulation") {
		const simulationType = params.simulationType || "general"
		params.amount = simulationType === "general" ? SIMULATION_DEFAULT_NUMBER_QUESTIONS_GENERAL : SIMULATION_DEFAULT_NUMBER_QUESTIONS_SPECIFIC
		params.time = simulationType === "general" ? SIMULATION_DEFAULT_TIME_GENERAL : SIMULATION_DEFAULT_TIME_SPECIFIC
	}

	if (!params.topics) {
		params.topics = params.simulationType == "general" ? Object.keys(topicsGeneral): Object.keys(topicsSpecific)
	}

	// Update state with form data
	updateState({ filters: params })

	// Navigate to the appropriate page based on the form type
	loadComponent(type === "study" ? "/study/play" : "/simulation/play")
}
