// navigation.js

import htmx from "htmx.org"

import { fetchAPI } from "../api.js"
import { WEB_VERSION } from "../environment.js"
import { showModalX } from "../modals.js"
import { loadComponent } from "../router.js"
import { getState, updateState } from "../states.js"
import { showConfirmationDialog } from "./components.js"
import { loginData, navItems, navLogoData, supportItems, upcomingEvents, userProfileData } from "./data.js"
import { toggleHiddenClass } from "./utils.js"

import * as skeleton from "./skeleton.js"
import * as utils from "./utils.js"

export function initializeNavigation() {
	// 1. Immediate UI initialization with loading states
	initializeLoadingState()

	// 2. Load data asynchronously
	loadNavigationData().catch(error => {
		console.error("Failed to load navigation:", error)
	})
}

function initializeLoadingState() {
	// Initialize version with loading state
	document.querySelectorAll("[data-version-box]").forEach(versionBox => {
		versionBox.textContent = "Version: Loading..."
	})

	// skeleton.renderNavLogoSkeleton()
	skeleton.renderNavSkeleton()
	skeleton.renderSupportSkeleton()
	// skeleton.renderEventsSkeleton()
	skeleton.renderProfileSkeleton()
}

async function retrieveUserDataFromDB() {
	const { response, error } = await fetchAPI("USERS.SELF")
	if (error) {
		console.error("navigation.js - retrieveUserDataFromDB: User data not found.", error.message, error.details)
		return
	}
	return response.data.user
}

function hideNavigation() {
	document.querySelector("#nav-container").classList.add("hidden")
	document.querySelector("#open-nav-button").classList.add("hidden")
	document.querySelector("#main-container").classList.remove("lg:pl-64")
}

function showNavigation() {
	document.querySelector("#nav-container").classList.remove("hidden")
	document.querySelector("#open-nav-button").classList.remove("hidden")
	document.querySelector("#main-container").classList.add("lg:pl-64")
}

async function loadNavigationData() {
	// TEST: wait for 10 seconds
	// await new Promise(resolve => setTimeout(resolve, 10000))

	try {
		// Parallel data fetching
		const [userDB, authState] = await Promise.all([
			retrieveUserDataFromDB(),
			getState()
		])

		if (!userDB) {
			updateState({ isAuthenticated: false, user: {} })
			console.error("User data not found. Redirecting to auth page.")
			hideNavigation()
			loadComponent("/auth")
			return
		}

		updateNavLogo(navLogoData)
		renderLoginButton(loginData)
		// Update UI progressively as data becomes available
		updateVersion(WEB_VERSION)
		createSupportSection()
		// createUpcomingEventsSection(upcomingEvents)

		// Update auth-dependent UI last
		// authState.isAuthenticated ? backdrop(false) : backdrop(true)
		authState.isAuthenticated ? showNavigation() : hideNavigation()
		if (authState.isAuthenticated) renderUserProfile(userProfileData, userDB)
		toggleHiddenClass("[data-user-container]", !authState.isAuthenticated)
		toggleHiddenClass("[data-login-container]", authState.isAuthenticated)

		if (authState.isAuthenticated) createNav()
		initializeMobileNavigation()

		document.querySelectorAll("[data-theme-toggle]").forEach(btn => {
			btn.addEventListener("click", utils.toggleTheme)
		})

	} catch (error) {
		console.error("Error loading navigation data:", error)
		showErrorFallbacks()
	}
}

function updateVersion(version) {
	document.querySelectorAll("[data-version-box]").forEach(versionBox => {
		versionBox.textContent = `Version: ${version ? version : "---"}`
	})
}

function showErrorFallbacks() {
	const elements = {
		"[data-version-box]":   `Version: ${WEB_VERSION}`,
		"[data-brand-name]":         "", // Navigation Unavailable
		"[data-nav-links]":     "<div class='text-red-500'></div>", // Menu loading failed
		"[data-support-links]": "<div class='text-red-500'></div>", // Support unavailable
		"[data-latest-links]":  "<div class='text-red-500'></div>" // Events unavailable
	}

	Object.entries(elements).forEach(([selector, fallback]) => {
		const element = document.querySelector(selector)
		if (element) element.innerHTML = fallback
	})
}

function initializeMobileNavigation() {
	const mobileNavContainer = document.querySelector("#mobile-nav-container")
	const mobileNavContent = document.querySelector("#mobile-nav-content")
	const desktopNavContent = document.querySelector("#nav-container").innerHTML
	const openNavButton = document.querySelector("#open-nav-button")
	const closeNavButton = document.querySelector("#close-nav-button")
	let isNavOpen = false

	// Populate mobile nav content
	mobileNavContent.innerHTML = desktopNavContent

	// FIXME: Remove max-lg:hidden classes from mobile nav content
	mobileNavContent.querySelectorAll(".max-lg\\:hidden").forEach(el => {
		el.classList.remove("max-lg:hidden")
	})

	function openMobileNav() {
		mobileNavContainer.classList.remove("-translate-x-full")
		isNavOpen = true
		closeListener("attach")
	}

	function closeMobileNav() {
		mobileNavContainer.classList.add("-translate-x-full")
		isNavOpen = false
		closeListener("detach")
	}

	// Attach a close listener when navigation is open
	function closeListener(action) {
		if (action === "attach") document.addEventListener("click", handleOutsideClick)
		if (action === "detach") document.removeEventListener("click", handleOutsideClick)
	}

	openNavButton.addEventListener("click", function(event) {
		event.stopPropagation() // Prevent event from bubbling up
		openMobileNav()
	})

	closeNavButton.addEventListener("click", closeMobileNav)

	mobileNavContent.addEventListener("click", (event) => {
		// Use closest to find the <a> tag with the data attribute
		if (event.target.closest("[data-close-nav]")) closeMobileNav()
	})


	// Function to handle clicks outside the navigation
	function handleOutsideClick(event) {
		const isClickInsideNav = mobileNavContainer.contains(event.target)
		const isClickOnOpenButton = openNavButton.contains(event.target)

		// If click is outside nav and nav is open, close it
		if (!isClickInsideNav && !isClickOnOpenButton && isNavOpen) {
			closeMobileNav()
		}
	}

	// FIXME: Make this prettier plz
	document.querySelectorAll("[data-link]").forEach(link => {
		link.onclick = (e) => {
			e.preventDefault()
			const path = link.getAttribute("href")
			loadComponent(path)
		}
	})
	htmx.process(mobileNavContent)
}

function renderNavDropdown(menuItems) {
	document.querySelectorAll("[data-brand-dropdown]").forEach(brandDropdown => {
		brandDropdown.innerHTML = "" // Clear existing items

		menuItems.forEach(item => {
			const anchor = document.createElement("a")
			if (item.href) anchor.href = item.href
			anchor.classList.add(
				"flex", "items-center", "gap-3", "rounded-lg", "px-4", "py-2", "text-sm", "text-gray-500",
				"hover:bg-gray-50", "dark:hover:bg-gray-700", "dark:text-gray-300",
				"dark:hover:text-white"
			)
			anchor.setAttribute("role", "menuitem")
			anchor.setAttribute("data-link", true)

			// Add icon if provided
			if (item.icon) {
				const iconSpan = document.createElement("span")
				iconSpan.innerHTML = item.icon
				iconSpan.classList.add("w-5", "h-5", "text-gray-400", "dark:text-gray-500")
				anchor.appendChild(iconSpan)
			}

			// Add text
			const textSpan = document.createElement("span")
			textSpan.textContent = item.text
			anchor.appendChild(textSpan)

			// Add HTMX attributes if provided
			if (item.hxGet) {
				anchor.setAttribute("hx-get", item.hxGet)
			}
			if (item.hxTarget) {
				anchor.setAttribute("hx-target", item.hxTarget)
			}
			if (item.hxSwap) {
				anchor.setAttribute("hx-swap", item.hxSwap)
			}

			brandDropdown.appendChild(anchor)
		})
	})
}

function updateNavLogo(data) {
	const { avatar, name, icon, menuItems } = data

	// Update avatar
	document.querySelectorAll("[data-brand-logo]").forEach(brandLogo => {
		brandLogo.classList.remove("hidden")
		brandLogo.src = avatar.src
		brandLogo.alt = avatar.alt
	})

	// Update name
	document.querySelectorAll("[data-brand-name]").forEach(brandName => {
		brandName.classList.remove("animate-pulse")
		brandName.textContent = name
	})

	// Update icon
	document.querySelectorAll("[data-brand-icon]").forEach(brandIcon => {
		if (icon) brandIcon.outerHTML = icon
	})

	// Update dropdown menu items with HTMX support
	if (!menuItems || menuItems.length === 0) {
		// remove dropdown-menu from the DOM
		document.querySelectorAll("[data-dropdown-menu]").forEach(dropdownMenu => {
			dropdownMenu.remove()
		})
		document.querySelectorAll("[data-brand-icon]").forEach(brandIcon => {
			brandIcon.remove()
		})
	}

	renderNavDropdown(menuItems)
}

function createNav() {
	document.querySelectorAll("[data-nav-links]").forEach(navLinks => {
	// if (!navElementList) console.error("Nav element list not found")

		navLinks.innerHTML = "" // Clear existing items

		const navContainer = document.createElement("div")
		navContainer.classList.add("flex", "flex-col", "gap-0.5")

		navItems.forEach((item) => {
			const spanWrapper = document.createElement("span")
			spanWrapper.classList.add("relative")

			const link = document.createElement("a")
			link.classList.add(
				"flex", "w-full", "items-center", "gap-3", "rounded-lg", "px-2", "py-2.5",
				"text-left", "text-base/6", "font-medium", "text-zinc-950", "sm:py-2",
				"sm:text-sm/5", "dark:text-white", "hover:bg-gray-100", "dark:hover:bg-gray-700"
			)

			if (item.lock) link.classList.add("cursor-not-allowed", "opacity-50")
			if (!item.lock) link.href = item.href

			link.setAttribute("data-close-nav", true)

			switch (item.href) {
			// in case the href contains https, it will be treated as an external link and will open in a new tab (target="_blank")
			case item.href.match(/^https?:\/\//)?.input:
				link.setAttribute("target", "_blank")
				link.setAttribute("rel", "noopener noreferrer")
				break
			case "/classroom": // WARNING: THIS IS COMPLETELY EXPERIMENTAL
				break
				// NOTE: This way must trigger a custom initialization after content is loaded.
				// const componentsPath = "/src/web/components"
				// For that, a function must be defined in the page that will be called.
				// The state of the page must be changed manually too. Passed to the function as an argument?
				// console.warn("HTMX attributes set for", item.href)
				// console.warn("Classroom navigation using HTMX is experimental!")

				// link.setAttribute("hx-get", `${componentsPath}${item.href}.html`)
				// link.setAttribute("hx-target", "#main-content")
				// link.setAttribute("hx-swap", "innerHTML")
				break
			default:
				link.setAttribute("data-link", true)
				link.onclick = (e) => {
					e.preventDefault()
					const path = link.getAttribute("href")
					loadComponent(path)
				}
				break
			}

			// Create and add the icon
			const iconSpan = document.createElement("span")
			iconSpan.innerHTML = item.icon
			iconSpan.classList.add("size-6", "shrink-0", "fill-zinc-500", "dark:fill-zinc-400")
			link.appendChild(iconSpan)

			// Create and add the label
			const labelSpan = document.createElement("span")
			labelSpan.classList.add("truncate")
			labelSpan.textContent = item.label
			link.appendChild(labelSpan)

			// Append the link to the span wrapper
			spanWrapper.appendChild(link)

			// Append the span wrapper to the nav container
			navContainer.appendChild(spanWrapper)
		})

		htmx.process(navContainer)
		navLinks.appendChild(navContainer)
	})
}

function createSupportSection() {
	document.querySelectorAll("[data-support-links]").forEach(supportLinks => {
		supportLinks.innerHTML = ""

		const supportContainer = document.createElement("div")
		supportContainer.classList.add("flex", "flex-col", "gap-0.5")

		supportItems.forEach((item) => {
			const spanWrapper = document.createElement("span")
			spanWrapper.classList.add("relative")

			const link = document.createElement("a")
			link.classList.add(
				"flex", "w-full", "items-center", "gap-3", "rounded-lg", "px-2", "py-2.5",
				"text-left", "text-base/6", "font-medium", "text-zinc-950", "sm:py-2",
				"sm:text-sm/5", "dark:text-white", "cursor-pointer",
				"hover:bg-gray-100", "dark:hover:bg-gray-700"
			)
			if (link.href) link.href = item.href
			// if onclick event is detected, add it to the link:
			if (item.onclick) {
				link.setAttribute("onclick", item.onclick)
			}
			const iconWrapper = document.createElement("span")
			iconWrapper.innerHTML = item.icon // replace icon comment with SVG string

			// Additional hover and dark mode classes dynamically
			iconWrapper.classList.add("size-6", "shrink-0", "fill-zinc-500", "dark:fill-zinc-400")

			const labelSpan = document.createElement("span")
			labelSpan.classList.add("truncate")
			labelSpan.textContent = item.label

			if (item.icon) link.appendChild(iconWrapper)
			link.appendChild(labelSpan)
			spanWrapper.appendChild(link)
			supportContainer.appendChild(spanWrapper)
		})

		// Append this section to the appropriate place in your DOM
		supportLinks.appendChild(supportContainer)
	})
}

function createUpcomingEventsSection(events) {
	const eventsList = document.querySelector("[data-latest-links]")
	if (eventsList) eventsList.innerHTML = ""

	const sectionContainer = document.createElement("div")
	sectionContainer.classList.add("max-lg:hidden", "flex", "flex-col", "gap-0.5", "mt-5")

	// Adding the header
	const header = document.createElement("h3")
	header.classList.add("mb-1", "px-2", "text-xs/6", "font-medium", "text-zinc-500", "dark:text-zinc-400")
	header.textContent = "Próximos eventos"
	sectionContainer.appendChild(header)

	events.forEach((event) => {
		// Create a wrapper for each event
		const spanWrapper = document.createElement("span")
		spanWrapper.classList.add("relative", "group")

		// Create the link element
		const link = document.createElement("a")
		link.classList.add(
			"flex", "w-full", "items-center", "gap-3", "rounded-lg", "px-2", "py-2.5",
			"text-left", "text-base/6", "font-medium", "text-zinc-950", "sm:py-2",
			"sm:text-sm/5", "dark:text-white", "cursor-help", "truncate"
		)
		if (event.href) link.href = event.href

		// Add an icon if provided
		if (event.icon) {
			const iconWrapper = document.createElement("span")
			iconWrapper.classList.add("size-6", "shrink-0", "fill-zinc-500", "dark:fill-zinc-400")
			iconWrapper.innerHTML = event.icon
			link.appendChild(iconWrapper)
		}

		// Add label
		const labelSpan = document.createElement("span")
		labelSpan.classList.add("truncate")
		labelSpan.textContent = event.label
		link.appendChild(labelSpan)

		// Create the tooltip
		const tooltip = document.createElement("div")
		tooltip.classList.add(
			"fixed", "hidden", "group-hover:block", "bg-zinc-800", "dark:bg-zinc-700",
			"text-white", "text-xs", "py-1", "px-2", "rounded-lg", "shadow-lg",
			"left-0", "right-0", "mt-1", "w-max", "max-w-xs", "z-[9999]", "overflow-visible",
		)
		tooltip.textContent = event.label

		// Append elements to the wrapper
		spanWrapper.appendChild(link)
		spanWrapper.appendChild(tooltip)
		sectionContainer.appendChild(spanWrapper)
	})

	// Append this section to the appropriate place in your DOM
	eventsList.appendChild(sectionContainer)
}

function renderUserProfile(data, user) {
	const { src, alt, text, icon, menuItems } = data
	const { name, email, role, state } = user

	// Update avatar
	document.querySelectorAll("[data-user-avatar]").forEach(avatar => {
		avatar.classList.remove("animate-pulse")
		avatar.src = src
		avatar.alt = alt
	})

	// Update name
	document.querySelectorAll("[data-user-name]").forEach(nameSpan => {
		nameSpan.textContent = name
	})

	// Update icon
	document.querySelectorAll("[data-user-icon]").forEach(userIcon => {
		userIcon.classList.remove("animate-pulse")
		if (icon) userIcon.outerHTML = icon
	})

	// Update dropdown menu items with HTMX support
	document.querySelectorAll("[data-user-dropdown]").forEach(dropdownMenu => {
		dropdownMenu.innerHTML = "" // Clear existing items

		menuItems.forEach(item => {
			const anchor = document.createElement("a")
			if (item.href) {
				anchor.href = item.href
				anchor.setAttribute("data-link", true)
				anchor.setAttribute("data-close-nav", true)
			}
			anchor.classList.add(
				"flex", "items-center", "gap-2", "rounded-lg", "px-4", "py-2", "text-sm",
				"text-gray-500", "hover:bg-gray-50", "dark:hover:bg-gray-700",
				"dark:text-gray-300", "dark:hover:text-white", "cursor-pointer"
			)
			anchor.setAttribute("role", "menuitem")


			// Add icon if provided
			if (item.icon) {
				const iconSpan = document.createElement("span")
				iconSpan.innerHTML = item.icon
				iconSpan.classList.add("w-5", "h-5", "text-gray-400", "dark:text-gray-500")
				anchor.appendChild(iconSpan)
			}

			// Add menu item text
			const textSpan = document.createElement("span")
			textSpan.textContent = item.text
			anchor.appendChild(textSpan)

			// Add HTMX attributes if provided
			if (item.hxAttributes && Object.keys(item.hxAttributes).length > 0) {
				Object.entries(item.hxAttributes).forEach(([key, value]) => {
					anchor.setAttribute(key, value)
				})
			}

			if (item.hxNotify) {
				anchor.setAttribute("hx-confirm", "true")
				anchor.setAttribute("hx-on::after-request", `
					if (event.detail.successful) {
						showModalX('success', 'Estadísticas reiniciadas', 'Las estadísticas se han reiniciado correctamente');
					} else {
						showModalX('error', 'Error', event.detail.xhr.responseText || 'Ha ocurrido un error inesperado');
					}
					loadComponent('/')
				`)
				window.showModalX = showModalX
				window.loadComponent = loadComponent
			}

			dropdownMenu.appendChild(anchor)
			htmx.process(dropdownMenu)
		})

	})
}

document.addEventListener("htmx:confirm", async function(e) {
	if (!e.detail.target.hasAttribute("hx-confirm")) return
	e.preventDefault()

	const userConfirmation = await showConfirmationDialog("Con esta acción borrarás todas las estadísticas vinculadas a tu perfil. ¿Estás seguro de que deseas continuar?")
	if (userConfirmation) e.detail.issueRequest(true)
})

function renderLoginButton(data) {
	const { src, alt, text, href } = data

	// Update avatar
	document.querySelectorAll("[data-login-avatar]").forEach(avatar => {
		avatar.classList.remove("animate-pulse")
		avatar.src = src
		avatar.alt = alt
	})

	// Update name
	document.querySelectorAll("[data-login-name]").forEach(name => {
		name.textContent = text
	})

	document.querySelectorAll("[data-login-anchor]").forEach(button => {
		button.href = href
		button.setAttribute("data-link", true)
	})

	// On click, redirect to login page
	document.querySelectorAll("[data-login-container").forEach(loginContainer => {
		loginContainer.setAttribute("data-close-nav", true)
		// loginContainer.addEventListener("click", () => {
		// 	loadComponent(href)
		// })
	})
}

export function isDesktop() {
	const DESKTOP_BREAKPOINT = 1024 // lg breakpoint in Tailwind CSS
    return window.innerWidth >= DESKTOP_BREAKPOINT
}

export function backdrop(isActive) {
	if (!isDesktop()) return

	if (!isActive) {
		const backdrop = document.querySelector("#nav-backdrop")
		if (backdrop) backdrop.remove()
		return
	}

	const navigation = document.querySelector("#nav-container")
	const backdrop = document.createElement("div")
	backdrop.id = "nav-backdrop"
	backdrop.className = "fixed inset-y-0 left-0 w-full max-w-80 lg:w-64 z-40 backdrop-blur-md bg-white/30 dark:bg-zinc-800/10 lg:block"
	navigation.parentNode.insertBefore(backdrop, navigation)
}
