import { fetchAPI } from "../api.js"
import { Component } from "../components.js"
import { showModalX } from "../modals.js"
import { getState } from "../states.js"
import { translations, userLanguage } from "../translations.js"
import {
	createNavLink,
	generateButton,
	generateDialog,
	generateSwitch
} from "./components.js"
import { initializeFilters } from "./filters.js"

import failIcon from "../img/fail.svg"
import passIcon from "../img/pass.svg"
import { loadComponent } from "../router.js"


class Results extends Component{
	constructor(type) {
		super("results")
		this.type = type // e.g., "history", "study" or "simulation"
	}

	initializeTestResult() {
		const { data } = getState()
		// Redirect to filters page if no filter data is found
		if (!data || Object.keys(data).length === 0) {
			console.error(`No filter data found, redirecting to ${this.type} filters page.`)
			return initializeFilters(this.type) // Redirect to the study-specific filters
		}

		return {submissionType: this.type, answers: data}
	}

	initializeHistoryResult() {
		const { submissions, currentRoute } = getState()

		if (!submissions?.length) {
			console.error(`No submission data found for ${this.type}, redirecting to home.`)
			loadComponent("/")
			return
		}

		// Get the id from the url, as will come in the form of /results/123
		const resultID = Number(currentRoute.split("/").pop())
		if (!resultID) {
			console.error("No result ID found in the URL")
			loadComponent("/")
			return
		}

		for (const submission of submissions) {
			if (submission.id === resultID) {
				const answers = JSON.parse(JSON.parse(submission.answers))
				return {
					submissionType: submission.submission_type,
					answers:        answers.map(q => ({
						id:             q.question_id,
						question:       q.question_text,
						accepted:       q.correct_answer,
						selectedAnswer: q.selected_answer,
						answers:        JSON.stringify(q.answers),
						explanation:    q.explanation || "",
					}))
				}
			}
		}

		console.error(`No submission data found for ${this.type}, redirecting to home.`)
		loadComponent("/")
	}

	async initialize() {
		let data

		switch (this.type) {
		case "study":
		case "simulation":
			data = this.initializeTestResult()
			break
		case "history":
			data = this.initializeHistoryResult()
			break
		default:
			showModalX("error", "Error", "El tipo de resultados es desconocido")
			break
		}

		this.renderQuizResults(data.submissionType, data.answers)
	}

	// Main render function to generate the entire layout
	renderQuizResults(submissionType, questions) {
		const root = document.getElementById("resultsForm")

		const backlink = createNavLink("/", "Inicio")
		// FIXME: Could this be made more generic?
		backlink.onclick = (e) => {
			e.preventDefault()
			const path = backlink.getAttribute("href")
			loadComponent(path)
		}
		backlink.classList.add("mb-10")
		root.appendChild(backlink)

		// Questions section
		const questionsSection = document.createElement("div")
		questionsSection.className = ""

		const containerQuestions = []
		const stats = { correct: 0, incorrect: 0, unanswered: 0 }
		questions.forEach(question => {
			if (question.selectedAnswer === undefined || question.selectedAnswer === "" || question.selectedAnswer === null) stats.unanswered++
			else if (question.selectedAnswer === question.accepted) stats.correct++
			else stats.incorrect++

			const genquestion = generateQuestion(
				question,

			)
			containerQuestions.push(
				genquestion
			)
		})

		questionsSection.append(...containerQuestions)

		const headerTitle = `${submissionType === "study" ? "Estudio" : submissionType === "simulation" ? "Simulacro" : ""}`
		const header = generateHeader(`${headerTitle}`)
		const subHeader = generateSubHeader(stats.correct, stats.incorrect, stats.unanswered)

		// Buttons
		const buttonsContainer = document.createElement("div")
		buttonsContainer.className = "flex flex-wrap gap-4 justify-center items-center p-4"

		if (this.type !== "history") {
			const retryButton = generateButton("retry", "Reintentar", "button", "secondary")
			retryButton.addEventListener("click", () => {
				loadComponent(`${this.type === "study" ? "/study/filters" : "/simulation/filters"}`)
			})
			buttonsContainer.append(retryButton)
		}

		const homeButton = generateButton("confirm", "Volver al inicio", "button", "primary")
		homeButton.addEventListener("click", () => {
			loadComponent("/")
		})
		buttonsContainer.append(homeButton)

		root.append(header, subHeader, questionsSection, buttonsContainer)
	}

}

// Generate the header section
function generateHeader(titleText) {
	const header = document.createElement("div")
	header.className = "w-full bg-zinc-200 dark:bg-zinc-700 text-black dark:text-white py-4 px-6 text-center font-semibold text-lg rounded-t-lg "
	header.textContent = `Resultados ${titleText}`
	return header
}

// Generate the subheader with score summary and explanation toggle
function generateSubHeader(correct, incorrect, unanswered) {
	const subHeader = document.createElement("div")
	subHeader.className = "w-full flex flex-wrap items-center justify-between bg-zinc-100 dark:bg-zinc-800 gap-4 px-4 py-2 border-b border-zinc-300 dark:border-zinc-700 rounded-b-lg"

	// Score summary
	const scoreSummary = document.createElement("div")
	scoreSummary.className = "w-full sm:w-auto flex flex-shrink-0 items-center justify-center gap-4 text-zinc-700 dark:text-zinc-300 text-sm"

	// Correct, Incorrect, and Unanswered
	const correctSpan = document.createElement("span")
	correctSpan.className = "flex items-center gap-1 text-green-600"
	correctSpan.innerHTML = `<span class="w-2.5 h-2.5 bg-green-600 rounded-full inline-block"></span>${correct} correcta${(correct > 1) ? "s" : ""}`

	const incorrectSpan = document.createElement("span")
	incorrectSpan.className = "flex items-center gap-1 text-red-600"
	incorrectSpan.innerHTML = `<span class="w-2.5 h-2.5 bg-red-600 rounded-full inline-block"></span>${incorrect} incorrecta${(incorrect > 1) ? "s" : ""}`

	const unansweredSpan = document.createElement("span")
	unansweredSpan.className = "flex items-center gap-1 text-yellow-500"
	unansweredSpan.innerHTML = `<span class="w-2.5 h-2.5 bg-yellow-500 rounded-full inline-block"></span>${unanswered} en blanco`

	scoreSummary.append(correctSpan, incorrectSpan, unansweredSpan)

	// Explanation toggle switch
	const explanationToggle = generateSwitch("explanationToggle", "Explicación", "justify-center")
	explanationToggle.addEventListener("change", (event) => {
		const allExplanations = document.querySelectorAll("[data-accordion]")
		const shouldExpand = event.target.checked

		allExplanations.forEach((accordion) => {
			const button = accordion.querySelector("button")
			const content = accordion.querySelector(".accordion-content")

			if (shouldExpand) {
				content.classList.remove("hidden")
				button.querySelector("svg").classList.add("rotate-180")
			} else {
				content.classList.add("hidden")
				button.querySelector("svg").classList.remove("rotate-180")
			}
		})
	})

	// Append score and toggle to subheader
	subHeader.appendChild(scoreSummary)
	subHeader.appendChild(explanationToggle)

	return subHeader
}

// Helper function to create an icon element
function createIcon(iconPath) {
	const icon = document.createElement("img")
	icon.src = iconPath
	icon.className = "w-4 h-4"
	return icon
}

function generateQuestion(question) {
	const questionText = question.question
	const answers = JSON.parse(question.answers)
	const correctAnswer = question.accepted
	const userAnswer = question.selectedAnswer
	const explanation = question.explanation

	const questionWrapper = document.createElement("div")
	questionWrapper.className = "relative my-4 px-4 py-2 pr-12 border border-zinc-300 dark:border-zinc-700 rounded-lg bg-white dark:bg-zinc-900"

	// Question text
	const questionTitle = document.createElement("p")
	questionTitle.className = "font-medium text-zinc-700 dark:text-zinc-300 mb-2"
	questionTitle.textContent = questionText
	questionWrapper.appendChild(questionTitle)

	// Define styles based on answer conditions
	const styles = {
		correct:           { border: "border border-green-600 border-dashed", icon: createIcon(passIcon), radio: "" },
		incorrect:         { border: "border border-red-600 border-dashed", icon: createIcon(failIcon), radio: "" },
		unansweredCorrect: { border: "border border-green-600 border-dashed", icon: null, radio: "border border-yellow-500" },
		default:           { border: "", icon: null, radio: "border border-zinc-400" }
	}

	answers.forEach((answer, index) => {
		const optionWrapper = document.createElement("div")
		optionWrapper.className = "flex items-center gap-2 mb-1 p-1"

		// Determine style based on correctness
		const isCorrect = index === correctAnswer
		const isUserAnswer = index === userAnswer
		const style = isCorrect && (userAnswer === undefined || userAnswer === "" || userAnswer === null) ? styles.unansweredCorrect
			: isCorrect ? styles.correct
				: isUserAnswer ? styles.incorrect
					: styles.default

		// Create a nested div for border styling around the icon and label
		const borderedContent = document.createElement("div")
		borderedContent.className = `flex items-center gap-2 p-1 rounded-md ${style.border}`

		// Create radio element or icon
		const radio = document.createElement("div")
		radio.className = `flex flex-shrink-0 w-4 h-4 rounded-full ${style.radio}`
		borderedContent.appendChild(style.icon || radio)

		// Option label
		const optionLabel = document.createElement("span")
		optionLabel.className = "text-sm text-zinc-700 dark:text-zinc-400"
		optionLabel.textContent = `${String.fromCharCode(65 + index)}. ${answer}`
		borderedContent.appendChild(optionLabel)

		// Append bordered content to option wrapper
		optionWrapper.appendChild(borderedContent)
		questionWrapper.appendChild(optionWrapper)
	})

	// Add a dropdown menu for each question
	const menuButton = document.createElement("button")
	menuButton.className = "absolute top-2 right-2 p-1 rounded-md text-zinc-500 hover:bg-zinc-200 dark:hover:bg-zinc-700"
	menuButton.innerHTML = "<svg class=\"w-5 h-5\" fill=\"currentColor\" viewBox=\"0 0 20 20\" xmlns=\"http://www.w3.org/2000/svg\"><path d=\"M10 3a1.5 1.5 0 110-3 1.5 1.5 0 010 3zm0 8a1.5 1.5 0 110-3 1.5 1.5 0 010 3zm0 8a1.5 1.5 0 110-3 1.5 1.5 0 010 3z\"/></svg>"

	// Dropdown menu container
	const dropdownMenu = document.createElement("div")
	dropdownMenu.className = "p-2 absolute top-8 right-2 w-48 bg-white dark:bg-zinc-800 rounded-md shadow-lg hidden"
	dropdownMenu.setAttribute("role", "menu")

	const menuItems = [
		{
			text: translations[userLanguage].openDispute,
			type : "dispute",
			action: async (e, question, anchor) => {
				e.preventDefault()

				let dialogRef = null
				const textarea = document.createElement("textarea")
				textarea.className = "w-full p-2 dark:text-white border rounded-lg dark:bg-zinc-800 dark:border-zinc-700"
				textarea.placeholder = translations[userLanguage].enterDisputeReason

				dialogRef = generateDialog({
					children:  [textarea],
					onConfirm: async () => {
						const reason = textarea.value.trim()
						if (!reason) {
							showModalX("error", "Error", "Please enter a reason for the dispute")
							return
						}

						try {
							const { response, error } = await fetchAPI("QUESTIONS.DISPUTES", "POST", { id: question.id }, { reason: JSON.stringify(reason) } )
							if (error || !response) {
								showModalX("error", "Error", error.message || "Un error ha ocurrido al enviar la disputa")
								return
							}

							showModalX("success", "Disputa realizada", "Tu disputa ha sido enviada con éxito, la contrastaremos lo antes posible.")
							// Disable dispute option
							anchor.classList.add("opacity-50", "cursor-not-allowed")
							anchor.classList.remove("hover:bg-gray-50", "cursor-pointer")
							anchor.onclick = null

						} catch (error) {
							showModalX("error", "Error", error.message || "Un error ha ocurrido al enviar la disputa")
						}
					},
					confirmText: translations[userLanguage].confirm,
					cancelText:  translations[userLanguage].cancel
				})

				dialogRef.showDialog()
			}
		},
		{
			text:   "No volver a mostrar",
			action: async (e, question, anchor) => {
				e.preventDefault()

				let dialogRef = null
				const info = document.createElement("div")
				info.className = "w-full p-2 dark:text-white rounded-lg dark:bg-zinc-800"
				info.textContent = translations[userLanguage].blacklistedInfo

				dialogRef = generateDialog({
					children:  [info],
					onConfirm: async () => {
						try {
							const { response, error } = await fetchAPI("QUESTIONS.BLACKLIST", "POST", { id: question.id } )
							if (error || !response) {
								showModalX("error", "Error", error.message || translations[userLanguage].blacklistError)
								return
							}

							showModalX("success", "Confirmado", translations[userLanguage].blackListSuccess)
							// Disable blacklist option
							anchor.classList.remove("hover:bg-gray-50", "cursor-pointer")
							anchor.classList.add("opacity-50", "cursor-not-allowed")
							anchor.onclick = null

						} catch (error) {
							showModalX("error", "Error", error.message || translations[userLanguage].blacklistError)
						}
					},
					confirmText: translations[userLanguage].confirm,
					cancelText:  translations[userLanguage].cancel
				})

				dialogRef.showDialog()
			}
		}
	]

	// Menu items loop remains the same
	menuItems.forEach((item) => {
		const anchor = document.createElement("a")
		anchor.className = "block px-4 py-2 text-sm text-gray-500 hover:rounded-lg hover:bg-gray-50 dark:hover:bg-gray-700 dark:text-gray-300 dark:hover:text-white cursor-pointer"
		anchor.textContent = item.text

		const { disputes } = getState()
		// Check if this question already has a dispute
		const isDisputed = item.type === "dispute" &&
			disputes?.some(dispute => dispute.question_id === question.id)

		if (isDisputed) { // Disable the dispute option
			anchor.classList.add("opacity-50", "cursor-not-allowed")
			anchor.classList.remove("hover:bg-gray-50", "cursor-pointer")
			anchor.onclick = null
		} else {
			// Only add onclick handler if not disputed
			anchor.onclick = (e) => item.action(e, question, anchor)
		}

		dropdownMenu.appendChild(anchor)
	})

	menuButton.addEventListener("click", (e) => {
		e.stopPropagation()
		e.preventDefault()
		dropdownMenu.classList.toggle("hidden")
	})

	document.addEventListener("click", () => {
		dropdownMenu.classList.add("hidden")
	})

	questionWrapper.appendChild(menuButton)
	questionWrapper.appendChild(dropdownMenu)

	// Accordion for explanation
	if (explanation) {
		const accordionWrapper = document.createElement("div")
		accordionWrapper.className = "w-full mt-4"
		accordionWrapper.setAttribute("data-accordion", "")

		const accordionButton = document.createElement("button")
		accordionButton.type = "button"
		accordionButton.className = "flex items-center justify-between w-full text-left py-2 text-xs sm:text-sm font-semibold text-zinc-900 dark:text-zinc-100"
		accordionButton.innerHTML = `
			<span>Ver explicación</span>
			<svg class="h-5 w-5 transform transition-transform duration-300 ease-in-out" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke-width="2" stroke="currentColor">
				<path stroke-linecap="round" stroke-linejoin="round" d="M19 9l-7 7-7-7"></path>
			</svg>
		`

		const accordionContent = document.createElement("div")
		accordionContent.className = "accordion-content mt-2 overflow-hidden text-xs sm:text-sm text-zinc-900 dark:text-zinc-100 hidden"
		accordionContent.textContent = explanation

		let expanded = false
		accordionButton.addEventListener("click", () => {
			expanded = !expanded
			accordionContent.classList.toggle("hidden")
			accordionButton.querySelector("svg").classList.toggle("rotate-180")
		})

		accordionWrapper.appendChild(accordionButton)
		accordionWrapper.appendChild(accordionContent)
		questionWrapper.appendChild(accordionWrapper)
	}

	return questionWrapper
}


export const resultsComponent = new Results("results")
// Initialize filters with type parameter
export const initializeResults = (type) => {
	const filtersComponent = new Results(type)
	filtersComponent.initialize()
}
