// home.js

import { fetchAPI } from "../api.js"
import { Component } from "../components.js"
import { loadComponent } from "../router.js"
import { generateSubmissionsTable } from "./components.js"
import { DASHBOARD_TABLE_HISTORY_AMOUNT, SUCCESS_THRESHOLD } from "../environment.js"
import { updateState } from "../states.js"
import { showModalX } from "../modals.js"

// Define the Home component extending from the base Component class
class Home extends Component{
	constructor() {
		super("home") // Initialize with the component name "home"
	}

	async initialize() {
		// Immediately render skeleton UI
		this.renderLoadingState()

		// Fetch data asynchronously
		this.loadData().catch(error => {
			console.error("Failed to load data:", error)
			// Handle error state
		})
	}

	renderLoadingState() {
		// Show loading states for each section
		this.updateGreeting("", "Cargando...")
		this.renderCardSkeletons()
		this.renderTableSkeleton()

		// FIXME: Enable the dropdown when the data is available
		const preparePeriodsData = null
		this.populatePeriodSelect(this.preparePeriods(preparePeriodsData))
		const dropdownContainer = document.querySelector("#dropdown-container")
		if (!preparePeriodsData && dropdownContainer) {
			dropdownContainer.remove()
		}
	}

	renderCardSkeletons() {
		const cards = Array(4).fill({
			title:       "Cargando...",
			value:       "---",
			description: "Cargando datos...",
			from:        "",
			changeColor: "bg-gray-200 dark:bg-gray-700"
		})
		this.createDashboardCards(cards)
	}

	renderTableSkeleton() {
		const tableContainer = document.querySelector("#submissions-table")
		if (!tableContainer) return
		tableContainer.innerHTML = `
            <div class="animate-pulse">
                <div class="h-4 bg-gray-200 dark:bg-gray-700 rounded w-3/4 mb-4"></div>
                <div class="h-4 bg-gray-200 dark:bg-gray-700 rounded w-1/2 mb-4"></div>
                <div class="h-4 bg-gray-200 dark:bg-gray-700 rounded w-5/6"></div>
            </div>
        `
	}

	async loadData() {
		// TEST: wait for 10 seconds
		// await new Promise(resolve => setTimeout(resolve, 10000))

		// Fetch all data in parallel
		const [userDB, submissions] = await Promise.all([
			this.retrieveUserDataFromDB(),
			this.prepareDashBoardTable()
		])

		if (!userDB) {
			console.error("User data not found. Redirecting to auth page.")
			updateState({ isAuthenticated: false, user: {} })
			loadComponent("/auth")
			return
		}

		const userDisputes = userDB.disputes || {}
		updateState({disputes: userDisputes }) // FIXME: Is this messed up? Should it be handled homogeneous with the rest of the data?

		// Update UI as data becomes available
		this.updateGreeting(userDB.name, "Hola")
		this.createDashboardCards(this.prepareDashboardCards(userDB.stats))
		this.createDashboardTable(submissions)
	}

	async prepareDashBoardTable() {
		try {
			const { response, error } = await fetchAPI("QUESTIONS.SUBMISSIONS", "GET", { amount: DASHBOARD_TABLE_HISTORY_AMOUNT })
			if (error || !response) {
				showModalX("error", "Error", error.message || "Error al cargar los resultados")
				return []
			}

			// Check if the response is an array
			if (!Array.isArray(response)) {
				console.error("Unexpected response format:", response)
				return []
			}

			updateState({ submissions: response })

			// Convert the response data to a table format
			return response.map((submission) => {
				let answers
				try {
					// Parse the answers JSON
					answers = JSON.parse(JSON.parse(submission.answers)) // FIXME: Double parse
				} catch (parseError) {
					console.error("Error parsing answers:", parseError)
					answers = []
				}

				// Calculate the score
				const score = answers.filter((answer) => answer.is_correct).length
				const total = answers.length

				// Determine the status based on the score
				const status = score >= Math.ceil(total * SUCCESS_THRESHOLD) ? "APTO" : "NO APTO"

				// Format the date
				const date = new Date(submission.created_at).toLocaleString("es-ES", {
					year:   "numeric",
					month:  "short",
					day:    "numeric",
					hour:   "2-digit",
					minute: "2-digit",
					hour12: false // Use 24-hour format
				})

				// Return the formatted table row object
				return {
					id:     submission.id,
					date:   date,
					type:   submission.submission_type === "simulation" ? "Simulacro" : "Estudio",
					score:  `${score}/${total}`,
					status: status,
					link:   `/results/${submission.id}`
				}
			})
		} catch (err) {
			console.error("Unexpected error in prepareDashBoardTable:", err)
			return []
		}

	}

	createDashboardTable(submissions) {
		// Render the table
		const tableContainer = document.querySelector("#submissions-table")
		if (!tableContainer) return
		if (submissions.length === 0) {
			tableContainer.innerHTML = "<p class='mt-3 text-sm/6 sm:text-xs/6 text-zinc-500'>Aún no hay resultados recientes</p>"
			return
		}
		if (tableContainer.childElementCount > 0) {
			// console.warn("Updating an already existing table")
			tableContainer.innerHTML = "" // Clear existing content
		}
		const tableElement = generateSubmissionsTable(submissions)
		tableContainer.appendChild(tableElement)
	}

	async retrieveUserDataFromDB() {
		const { response, error } = await fetchAPI("USERS.SELF")
		if (error) {
			showModalX("error", error.message, error.details)
			return
		}
		return response.data.user
	}

	updateGreeting(name, greeting) {
		const greetingTitle = document.querySelector("#greetings-title")
		if (greetingTitle) {
			greetingTitle.textContent = `${greeting}, ${name}`
		}
	}

	preparePeriods(stats) {
		// FIXME: HARDCODED. Add more periods as needed
		const periods = [{ value: "current_month", label: "Mes actual", hxGet: "", hxTarget: "", hxSwap: "" }]
		return periods
	}

	prepareDashboardCards(stats) {
		// stats: { "MM": { answeredFailedRatio: X, answeredPercentage: Y, ...} }
		const currentDate = new Date().toISOString().slice(0, 7) // YYYY-MM format
		const currentMonth = currentDate.slice(5, 7) // MM format

		// FIXME: This must be any month, not just the current one
		// also, the current month should be the default the currentDate is not available yet
		const currentMonthStats = stats[currentMonth] || stats[currentDate] || {}

		const totalAnswered = currentMonthStats.questionsSucceededSumTotal + currentMonthStats.questionsFailedSumTotal || 0
		const engagementRate = (currentMonthStats.questionsListedTotal / currentMonthStats.totalQuestions) * 100 || 0
		const completionRate = (totalAnswered / currentMonthStats.totalQuestions) * 100 || 0
		const successRate = (currentMonthStats.questionsSucceededSumTotal / totalAnswered) * 100 || 0
		const failureRate = (currentMonthStats.questionsFailedSumTotal / totalAnswered) * 100 || 0
		const blankRate = (currentMonthStats.questionsBlankSumTotal / currentMonthStats.questionsListedSumTotal) * 100 || 0
		const successFailureRatio = (currentMonthStats.questionsSucceededSumTotal / currentMonthStats.questionsFailedSumTotal) || 0

		// Create cards with calculated metrics
		const dashboardCards = [
			{
				title:       "Engagement",
				value:       `${engagementRate.toFixed(2)}%`,
				description: "Porcentaje de preguntas con las que has interactuado",
				from:        "desde el total",
				changeColor: "bg-blue-400/20 text-blue-700 dark:bg-blue-400/10 dark:text-blue-300"
			},
			{
				title:       "Porcentaje de respuesta",
				value:       `${completionRate.toFixed(2)}%`,
				description: "Porcentaje de preguntas respondidas sobre el total",
				from:        "desde el total",
				changeColor: "bg-lime-400/20 text-lime-700 dark:bg-lime-400/10 dark:text-lime-300"
			},
			{
				title:       "Tasa de Éxito",
				value:       `${successRate.toFixed(2)}%`,
				description: "Porcentaje de preguntas correctas sobre las respondidas",
				from:        "desde el total de preguntas respondidas",
				changeColor: "bg-green-400/20 text-green-700 dark:bg-green-400/10 dark:text-green-300"
			},
			{
				title:       "Incorrectas",
				value:       `${failureRate.toFixed(2)}%`,
				description: "Porcentaje de preguntas incorrectas sobre las respondidas",
				from:        "desde el total de preguntas respondidas",
				changeColor: "bg-red-400/20 text-red-700 dark:bg-red-400/10 dark:text-red-300"
			},
			// {
			// 	title:       "En Blanco",
			// 	value:       `${blankRate.toFixed(2)}%`,
			// 	description: "Porcentaje de preguntas que dejaste en blanco",
			// 	from:        "del total de preguntas listadas",
			// 	changeColor: "bg-yellow-400/20 text-yellow-700 dark:bg-yellow-400/10 dark:text-yellow-300"
			// },
			{
				title:       "Ratio Correcto/Incorrecto",
				value:       `${successFailureRatio.toFixed(2)}`,
				description: "Relación entre preguntas correctas e incorrectas",
				from:        "desde el total de preguntas respondidas",
				changeColor: "bg-purple-400/20 text-purple-700 dark:bg-purple-400/10 dark:text-purple-300"
			}
		]

		return dashboardCards
	}

	createDashboardCards(overviewCards) {
		const overviewCardsContainer = document.querySelector("#overview-cards")
		overviewCardsContainer.innerHTML = "" // Clear existing content

		overviewCards.forEach((card) => {
			const cardDiv = document.createElement("div")

			// Create and append the hr element
			const hrElement = document.createElement("hr")
			hrElement.setAttribute("role", "presentation")
			hrElement.classList.add("w-full", "border-t", "border-zinc-950/10", "dark:border-white/10")
			cardDiv.appendChild(hrElement)

			// Create and append the title
			const titleDiv = document.createElement("div")
			titleDiv.classList.add("mt-6", "text-lg/6", "font-medium", "sm:text-sm/6", "dark:text-white")
			titleDiv.textContent = card.title
			cardDiv.appendChild(titleDiv)

			if (card.description) {
				// Create and append the description
				const descriptionSpan = document.createElement("span")
				descriptionSpan.classList.add("text-zinc-500", "text-sm/6", "sm:text-xs/6")
				descriptionSpan.textContent = card.description
				cardDiv.appendChild(descriptionSpan)
			}

			// Create and append the value
			const valueDiv = document.createElement("div")
			valueDiv.classList.add("mt-3", "text-3xl/8", "font-semibold", "sm:text-2xl/8", "dark:text-white")
			valueDiv.textContent = card.value
			cardDiv.appendChild(valueDiv)


			if (card.change) {
			// Create and append the change and description
				const changeContainer = document.createElement("div")
				changeContainer.classList.add("mt-3", "text-sm/6", "sm:text-xs/6")

				const changeSpan = document.createElement("span")
				changeSpan.classList.add("inline-flex", "items-center", "gap-x-1.5", "rounded-md", "px-1.5", "py-0.5", "text-sm/5", "font-medium", "sm:text-xs/5")
				card.changeColor.split(" ").forEach(cls => changeSpan.classList.add(cls))
				changeSpan.textContent = card.change

				const fromSpan = document.createElement("span")
				fromSpan.classList.add("text-zinc-500", "ml-1")
				fromSpan.textContent = card.from

				changeContainer.appendChild(changeSpan)
				changeContainer.appendChild(fromSpan)

				cardDiv.appendChild(changeContainer)
			}

			overviewCardsContainer.appendChild(cardDiv)
		})
	}

	populatePeriodSelect(periodOptions) {
		const selectElement = document.querySelector("#period-select")
		selectElement.innerHTML = "" // Clear existing options

		periodOptions.forEach(option => {
			const optElement = document.createElement("option")
			optElement.value = option.value
			optElement.textContent = option.label

			// Add HTMX attributes
			optElement.setAttribute("hx-get", option.hxGet)
			optElement.setAttribute("hx-target", option.hxTarget)
			optElement.setAttribute("hx-swap", option.hxSwap)

			selectElement.appendChild(optElement)
		})
	}
}

// Export and initialize the Home component
export const homeComponent = new Home("home")
export const initializeHome = () => homeComponent.initialize()
