// auth.js

import Alpine from "alpinejs"
import htmx from "htmx.org"

import { Component } from "../components.js"
import { SERVER_URL } from "../environment.js"
import { showModalX } from "../modals.js"
import { loadComponent } from "../router.js"
import { updateState } from "../states.js"
import { initializeNavigation } from "./navigation.js"

// Setup form endpoints
const forms = {
	"login":    `${SERVER_URL}login/`,
	"register": `${SERVER_URL}register/`,
	"forgot":   `${SERVER_URL}password/forgot/`,
	"reset":    `${SERVER_URL}password/reset/` // http://localhost:8080/auth/?action=reset&token=123123123
}

// Create a store named 'auth'
document.addEventListener("alpine:init", () => {
	Alpine.store("auth", {
		activeForm: "login"
	})
})

const passwordToggleIcons = {
	show: `
    <svg data-toggle-password-visibility="show" xmlns="http://www.w3.org/2000/svg"
		fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round"
		stroke-linejoin="round" class="h-5 w-5 text-zinc-900 dark:text-zinc-100 toggle-icon"
		viewBox="0 0 24 24">
		<path stroke="currentColor" stroke-width="2" stroke-linecap="round"
			stroke-linejoin="round" d="M1 12C1 12 5 4 12 4C19 4 23 12 23 12" />
		<path stroke="currentColor" stroke-width="2" stroke-linecap="round"
			stroke-linejoin="round" d="M1 12C1 12 5 20 12 20C19 20 23 12 23 12" />
		<circle stroke="currentColor" stroke-width="2" stroke-linecap="round"
			stroke-linejoin="round" cx="12" cy="12" r="3" />
	</svg>
  `,
	hide: `
    <svg data-toggle-password-visibility="hide" xmlns="http://www.w3.org/2000/svg"
		fill="none" stroke="currentColor" stroke-width="2" stroke-linecap="round"
		stroke-linejoin="round"
		class="h-5 w-5 text-zinc-900 dark:text-zinc-100 toggle-icon hidden"
		viewBox="0 0 22 22">
		<path stroke="currentColor" stroke-width="2" stroke-linecap="round"
			stroke-linejoin="round" d="M2 2L22 22" />
		<path stroke="currentColor" stroke-width="2" stroke-linecap="round"
			stroke-linejoin="round"
			d="M6.71277 6.7226C3.66479 8.79527 2 12 2 12C2 12 5.63636 19 12 19C14.0503 19 15.8174 18.2734 17.2711 17.2884M11 5.05822C11.3254 5.02013 11.6588 5 12 5C18.3636 5 22 12 22 12C22 12 21.3082 13.3317 20 14.8335" />
		<path stroke="currentColor" stroke-width="2" stroke-linecap="round"
			stroke-linejoin="round"
			d="M14 14.2362C13.4692 14.7112 12.7684 15.0001 12 15.0001C10.3431 15.0001 9 13.657 9 12.0001C9 11.1764 9.33193 10.4303 9.86932 9.88818" />
	</svg>
  `
}

// Define the Auth class extending Component
class Auth extends Component{
	constructor() {
		super("auth")
		this.setupFormListeners()// Setup form event listeners for handling response
	}

	async initialize() {
		this.initializePasswordToggle()// Initialize password visibility toggle functionality
		this.initializeResetFormListeners()

		// Checks if url contains the parameter 'verfified' and shows a modal if so.
		this.checkVerificationStatus()

		// Handle URL actions
		const params = new URLSearchParams(window.location.search)
		const action = params.get("action")
		// Use the official Alpine JS store setter
		if (action && ["login", "register", "forgot", "reset"].includes(action)) {
			Alpine.store("auth").activeForm = action
			if (action === "reset") this.checkResetToken()
		}


		Object.entries(forms).forEach(([formId, endpoint]) => {
			const form = document.querySelector(`#${formId}-form form`)
			if (form) form.setAttribute("hx-post", endpoint)
		})

		htmx.process(document.querySelector("#auth-forms"))
	}

	checkResetToken() {
		const params = new URLSearchParams(window.location.search)
		const token = params.get("token")
		const userID = params.get("userId")
		if (token && userID) {
			document.querySelector("#reset-token").value = token
			document.querySelector("#reset-user-id").value = userID
		}
	}

	checkVerificationStatus() {
		const params = new URLSearchParams(window.location.search)
		if (params.get("verified") === "true") {
			setTimeout(() => {
				showModalX("info", "Email verificado", "Tu dirección de correo electrónico ha sido verificada. Puedes iniciar sesión.")
			}, 1000)
			// Clean URL
			window.history.replaceState(null, null, "/auth")
		} else if (params.get("verified") === "false") {
			setTimeout(() => {
				showModalX("info", "No verificado", "Tu dirección de correo electrónico no ha podido ser verificada. Prueba a iniciar sesión, si no funciona, ponte en contacto con nosotros.")
			}, 1000)
			// Clean URL
			window.history.replaceState(null, null, "/auth")
		}
	}

	initializeResetFormListeners() {
		const form = document.getElementById("resetPasswordForm")
		const pass1 = document.getElementById("reset-password-1")
		const pass2 = document.getElementById("reset-password-2")
		const errorMsg = document.getElementById("error-message")

		form.addEventListener("htmx:beforeRequest", function(event) {
			// Clear old errors
			errorMsg.style.display = "none"
			errorMsg.textContent = ""

			// Check if either field is empty
			if (!pass1.value || !pass2.value) {
				event.preventDefault()
				errorMsg.textContent = "Por favor, ingresa ambas contraseñas."
				errorMsg.style.display = "block"
				return
			}

			// Check if they match
			if (pass1.value !== pass2.value) {
				event.preventDefault()
				errorMsg.textContent = "Las contraseñas no coinciden."
				errorMsg.style.display = "block"
				return
			}

			// If everything is valid, do nothing =>
			// HTMX will proceed with the form submission automatically
		})
	}

	initializePasswordToggle() {
		// console.log("Initializing password toggle")
		document.querySelectorAll(".toggle-password").forEach((toggleButton) => {
			// Insert the two icons (hide + show) as HTML
			toggleButton.innerHTML = passwordToggleIcons.hide + passwordToggleIcons.show

			const passwordInput = toggleButton.closest("div").querySelector("input[type='password'], input[type='text']")
			const showIcon = toggleButton.querySelector("[data-toggle-password-visibility='show']")
			const hideIcon = toggleButton.querySelector("[data-toggle-password-visibility='hide']")

			// Ensure "Show" is displayed initially, as the password field is hidden by default
			showIcon.classList.remove("hidden")
			hideIcon.classList.add("hidden")

			toggleButton.addEventListener("click", () => {
				// Toggle the input type
				const isPasswordHidden = passwordInput.type === "password"
				passwordInput.type = isPasswordHidden ? "text" : "password"

				// Toggle the visibility of the icons
				if (isPasswordHidden) {
					showIcon.classList.add("hidden")
					hideIcon.classList.remove("hidden")
				} else {
					showIcon.classList.remove("hidden")
					hideIcon.classList.add("hidden")
				}
			})
		})
	}

	setupFormListeners() {
		// FIXME: This is intercepting all htmx requests, not just the ones from the auth form
		document.addEventListener("htmx:afterRequest", async (event) => this.handleResponse(event)
			.catch(error => showModalX("error", "Error del servidor", `El servidor ha encontrado un error al procesar la solicitud: ${error.message}`)))

		// Ensure htmx includes credentials (cookies) in requests
		// document.addEventListener("htmx:configRequest", (event) => {
		// 	console.log("Request logged:", event)
		// 	event.detail.credentials = "include"
		// })
	}

	// contents of event.detail.xhr
	// {
	// 	"status": "error",
	// 	"statusCode": 403,
	// 	"error":	{
	// 		"code": "FORBIDDEN",
	// 		"message": "Permisos insuficientes para un recurso o acción.",
	// 		"details": "Please subscribe to access this service"
	// 	}
	// }
	async handleResponse(event) {
		// Check if the request path contains any of the form endpoints or return.
		// This is needed because of the htmx:afterRequest event listener setup on setupFormListeners()
		// FIXME: Find a better way to handle it
		if (!Object.keys(forms).some((endpoint) => event.detail.pathInfo.requestPath.includes(endpoint))) return

		const statusText = event.detail.xhr.statusText // e.g: "Forbidden", "Not Found", "Internal Server Error"...
		const status = event.detail.xhr.status // e.g: 200, 201, 400, 401, 403, 404, 500...
		const errorContents = JSON.parse(event.detail.xhr.response).error
		const requestPath = event.detail?.requestConfig?.path

		switch (status) {
		case 200:
			if (requestPath?.includes("password/forgot")) {
				showModalX("success", "Email enviado", "Se ha enviado un correo con las instrucciones para restablecer tu contraseña.")
				Alpine.store("auth").activeForm = "login"
			} else if (requestPath?.includes("password/reset")) {
				showModalX("success", "Contraseña actualizada", "Tu contraseña ha sido actualizada correctamente.")
				Alpine.store("auth").activeForm = "login"
			} else {
				// Handle login success
				updateState({
					isAuthenticated: true,
					user:            event.detail.xhr.response
				})

				initializeNavigation()
				loadComponent("/")
				showModalX("success", "Login satisfactorio", "Has iniciado sesión satisfactoriamente.")
			}
			break

		case 201:
			showModalX("success", "Registro satisfactorio", "Tu cuenta ha sido creada satisfactoriamente.")
			setTimeout(() => {
				showModalX("info", "Verificación pendiente", "Verifica tu email antes de iniciar sesión.")
			}, 1000)
			loadComponent("/auth")
			break

		default:
			showModalX("error", "No autorizado", `${ errorContents.message || "El servidor ha rechazado la solicitud."}`)
			break
		}
	}
}

// Exporting the auth component instance and initializer
export const authComponent = new Auth("auth")
export const initializeAuth = () => authComponent.initialize()
