// component.js
import { getState, updateState } from "./states.js"
import { showErrorModal } from "./modals.js"

export class Component{
	constructor(name) {
		this.name = name
		this.state = getState()
	}

	async initialize(filter = {}) {
		try {
			const data = await this.fetchData(filter)
			updateState({ [`${this.name}Data`]: data })
			this.generateContent(data)
			this.setupEventListeners()

		} catch (error) {
			console.error(`Error initializing ${this.name}:`, error)
			showErrorModal(error)
		}
	}

	async fetchData(filter) {
		throw new Error("fetchData method must be implemented")
	}

	generateContent(data) {
		throw new Error("generateContent method must be implemented")
	}

	setupEventListeners() {
		// Override if needed
	}
}
