// session.js
import { fetchAPI } from "./api.js"
import { REFRESH_TOKEN_TIME_INTERVAL, REFRESH_TOKEN_RENEW_CHECKPOINT } from "./environment.js"
import { showModalX } from "./modals.js"
import { updateState, getState } from "./states.js"
import { safeJSONParse } from "./utils.js"

class SessionManager{
	constructor() {
		this.isRefreshing = false
		this.failedQueue = []
		this.refreshAttempts = 0
		this.maxRefreshAttempts = 3
		this.refreshTimeout = null

		// this.setupNavigationBlockingRemovalOnPageRefresh()
	}

	setupNavigationBlockingRemovalOnPageRefresh() {
		window.addEventListener("beforeunload", () => {
			if (getState().navigationBlocked) {
				updateState({ navigationBlocked: false })
				return true // Show browser warning
			}
		})
	}

	setupAutoRefresh() {
		if (this.refreshTimeout) {
			clearInterval(this.refreshTimeout)
			this.refreshTimeout = null
		}

		// Initial check on load
		this.silentTokenCheck()

		// Set up periodic checks
		this.refreshTimeout = setInterval(() => {
			this.silentTokenCheck()
		}, 1000 * REFRESH_TOKEN_TIME_INTERVAL)

		// Cleanup on page unload
		window.addEventListener("beforeunload", () => {
			if (this.refreshTimeout) {
				clearInterval(this.refreshTimeout)
			}
		})
	}

	async refreshToken () {
		const { response, error } = await fetchAPI("AUTH.REFRESH", "POST")
		if (error || !response) {
			console.error("Error fetching submissions:", error)
			return false
		}

		if (!response.data?.refresh_token) {
			console.error("Refresh token not found in response")
			return false
		}

		return response.data.refresh_token
	}

	async silentTokenCheck() {
		if (this.isRefreshing) return // avoids concurrent checks
		this.isRefreshing = true

		const token = this.getTokenFromState()
		if (!token) return this.updateAuthState(false)

		if (this.isTokenCloseToExpiration(token)) {

			const newToken = await this.refreshToken()
			if (!newToken) {
				console.error("Token refresh failed")
				return this.updateAuthState(false)
			}

			this.updateAuthToken(newToken)
			this.updateAuthState(true)
		}

		this.isRefreshing = false // reset flag
	}

	expirationLeft(token) {
		const payload = JSON.parse(atob(token.split(".")?.[1]))
		const expiresIn = payload.exp - Math.floor(Date.now() / 1000)

		// Format for display
		const minutes = Math.floor(expiresIn / 60)
		const seconds = expiresIn % 60

		return `${minutes}m ${seconds}s (${expiresIn}s total)`
	}

	updateAuthState(isAuthenticated) {
		const state = getState()
		updateState({
			...state,
			isAuthenticated
		})
	}

	updateAuthToken(token) {
		const state = getState()
		updateState({
			...state,
			user: {
				...state.user,
				refresh_token: token
			}
		})
	}

	getTokenFromState() {
		const state = getState()
		try {
			// Validate state
			if (!state) {
				console.warn("No state found")
				return null
			}

			// Check for user data
			if (!state.user) {
				console.warn("No user data in state")
				return null
			}

			// Parse user data if needed
			const userData = typeof state.user === "string"
				? safeJSONParse(state.user) // Use our safer parser
				: state.user

			// Validate user data
			if (!userData) {
				console.warn("Invalid user data format")
				return null
			}

			// Check refresh token
			if (!userData.refresh_token) {
				console.warn("No refresh token found")
				return null
			}

			// Validate token format (should be JWT)
			if (typeof userData.refresh_token !== "string" ||
            !userData.refresh_token.split(".").length === 3) {
				console.warn("Invalid token format")
				return null
			}

			return userData.refresh_token

		} catch (error) {
			console.error("Error getting token from state:", {
				message: error.message,
				stack:   error.stack
			})
			return null
		}
	}

	/**
	 * Extracts expiration time from JWT token
	 * @param {string} token - JWT token
	 * @returns {number|null} Expiration timestamp in seconds or null if invalid
	 */
	getTokenExpiration(token) {
		if (!token || token.length < 0) {
			console.error("Invalid token")
			return null
		}

		try {
			const payload = JSON.parse(atob(token.split(".")[1]))
			return payload.exp
		}
		catch (error) {
			console.warn("Error getting token expiration from state:", error)
			return null
		}
	}

	isTokenExpired(token) {
		return this.getTokenExpiration(token) - Math.floor(Date.now() / 1000) < 0
	}

	isStateTokenExpired() {
		const token = this.getTokenFromState()
		return this.isTokenExpired(token)
	}

	isTokenCloseToExpiration(token) {
		return this.getTokenExpiration(token) - Math.floor(Date.now() / 1000) < REFRESH_TOKEN_RENEW_CHECKPOINT
	}

	isStateTokenCloseToExpiration() {
		const token = this.getTokenFromState()
		return this.isTokenCloseToExpiration(token)
	}

	/**
     * Process queued requests after token refresh
     * @param {Error|null} error - Error from token refresh
     * @param {string|null} token - New refresh token
     */
	processQueue(error, token = null) {
		this.failedQueue.forEach(prom => {
			if (error) {
				prom.reject(error)
			} else {
				prom.resolve(token)
			}
		})
		this.failedQueue = []
	}

	/**
     * Periodic token expiration check
     */
	checkIfTokenAboutToExpire() {
		if (this.isTokenCloseToExpiration()) {
			this.refreshToken().catch(error => {
				console.error("Auto refresh failed:", error)
				this.handleSessionExpired()
			})
		}
	}

	/**
     * Handle expired session
     */
	handleSessionExpired() {
		if (this.refreshTimeout) {
			clearTimeout(this.refreshTimeout)
		}
		showModalX("info", "Sesión concluida", "Tu sesión ha terminado. Si quieres volver a utilizar la aplicación, inicia sesión")
		this.clearSession()
		console.warn("Session expired")
		// loadComponent("/auth")
	}

	/**
     * Clear session state
     */
	clearSession() {
		this.refreshAttempts = 0
		updateState({
			isAuthenticated: false,
			user:            null
		})
	}
}

export const session = new SessionManager()
