// utils.js

import { mapServerErrorResponse, WebError } from "./errors.js"
import { showErrorModal } from "./modals.js"
import { SERVER_URL, QUESTIONS_SELF_ENDPOINT, DISPUTE_ENDPOINT, SUBMISSION_ENDPOINT, STATS_ENDPOINT } from "./environment.js"
import { API } from "./api.js"

import hideImg from "./img/hide.svg"
import showImg from "./img/show.svg"

export function embeddImageSrcBySelector(selector, src) {
	const elements = document.querySelectorAll(selector)
	elements.forEach((element) => {
		element.src = src
	})
}

export function initializePasswordToggle() {
	document.querySelectorAll(".toggle-password").forEach((toggleButton) => {
		toggleButton.addEventListener("click", () => {
			const passwordContainer = toggleButton.closest(".password-container")
			const passwordInput = passwordContainer.querySelector(".input-password")
			const isPasswordVisible = passwordInput.type === "password"
			passwordInput.type = isPasswordVisible ? "text" : "password"

			// Update toggle icon and alt text
			const icon = toggleButton.querySelector(".toggle-icon")
			icon.src = isPasswordVisible ? hideImg : showImg
			icon.alt = isPasswordVisible ? "Hide Password" : "Show Password"
		})
	})
}

// Utility function to perform a fetch request
export async function apiRequest(endpoint, method = "GET", options = {}) {
	try {
		// FIXME: remove this after full integration with the api contract
		// it removes the first slash from the endpoint if it exists
		if (endpoint.charAt(0) === "/") endpoint = endpoint.slice(1)

		const response = await fetch(SERVER_URL + endpoint, {
			method,
			headers:     { "Content-Type": "application/json" },
			credentials: "include",
			...options
		})

		if (!response.ok) {
			const error = await mapServerErrorResponse(response)
			return { response: null, error }
		}

		let data = null
		if (![101, 204, 205, 304].includes(response.status)) {
			data = await response.json()
		}

		return { response: data, error: null }
	} catch (error) {
		let detailedError
		console.error("Unknown error:", error) // FIXME: DEBUG
		if (error instanceof TypeError && error.message.includes("fetch")) {
			detailedError = new WebError(
				error,
				"NETWORK_ERROR",
				"Cannot connect to server. Please check your network connection and try again.",
			)
		} else {
			console.error("Unknown error:", error) // FIXME: DEBUG
			detailedError = new WebError(error, "UNKNOWN_ERROR", "An unknown error occurred")
		}

		showErrorModal(detailedError.toJSON())
		return { response: null, error: detailedError }
	}
}

// Fetch data from API
export async function fetchDataFromAPIUpdateSession(sessionKey, endpoint, options = {}) {
	const { response, error } = await apiRequest(endpoint, "GET", options)
	if (error) {
		console.error("Error fetching sessionItem from API:", {
			message: JSON.stringify(error.message),
			stack:   error.stack || "No stack available",
			details: error.details || "No details available"
		})
		return false
	}
	if (!response) {
		console.error("No response from API")
		return false
	}

	sessionStorage.setItem(sessionKey, JSON.stringify(response))
	return response
}

// Fetch data from sessionStorage or API
export async function fetchDataFromSessionOrAPI(sessionKey, endpoint, options = {}) {
	let sessionItem = sessionStorage.getItem(sessionKey)
	if (sessionItem) {
		try {
			sessionItem = JSON.parse(sessionItem)
		} catch (e) {
			console.error("Error parsing session data:", e)
			return false
		}
	}

	if (!sessionItem) {
		const { response, error } = await apiRequest(endpoint, "GET", options)
		if (error) {
			console.error("Error fetching sessionItem from API:", {
				message: error.message,
				stack:   error.stack || "No stack available",
				details: error.details || "No details available"
			})
			return false
		}
		sessionItem = response
		if (sessionItem) {
			sessionStorage.setItem(sessionKey, JSON.stringify(sessionItem))
		}
	}
	return sessionItem
}

// Fetch questions from the API
export async function getQuestionsFromDatabase(filters) {
	let urlparams = ""
	// if (!filters.amount) filters.amount = 10 // FIXME: DEFAULT AMOUNT
	if (filters.amount) {
		urlparams = `?amount=${filters.amount}`
	}
	// if filters.amount is provided, add to url

	return (await apiRequest(`${QUESTIONS_SELF_ENDPOINT}${urlparams}`)).response || []
}

// Fetch disputes from the API
export async function getCurrentUserDisputes() {
	return (await apiRequest(DISPUTE_ENDPOINT)).response || []
}

