// Helper function to shuffle an array
export function shuffleArray(array) {
	for (let i = array.length - 1; i > 0; i--) {
		const j = Math.floor(Math.random() * (i + 1));
		[array[i], array[j]] = [array[j], array[i]]
	}
}

export async function submitAnswers(answers, submissionType) {
	const bodyData = { answers, submissionType: submissionType || "undefined" }
	const {response, error} = await apiRequest(SUBMISSION_ENDPOINT, "POST", {
		body: JSON.stringify(bodyData),
	})
	if (error) { return { response, error } }

	// sessionStorage.setItem("lastSubmission", JSON.stringify(bodyData))

	// TODO: Hit an endpoint to update the user's stats after the submission
	// users/stats/
	const { response: statsResponse, error: statsError } = await apiRequest(STATS_ENDPOINT, "POST", {
		body: JSON.stringify(bodyData),
	})
	if (error) { return { statsResponse, statsError } }

	return {response, error}
}

export function extractSerializableResponse(response) {
	if (!(response instanceof Response)) {
		return response // If it's not a Response object, return as is
	}

	return {
		url:        response.url || null,
		status:     response.status || null,
		statusText: response.statusText || null,
		headers:    response.headers ? [...response.headers.entries()] : [], // Convert headers to an array of key-value pairs
		ok:         response.ok || false,
		redirected: response.redirected || false,
		type:       response.type || null,
		// Do not include body or bodyUsed directly, they are non-serializable
	}
}

export function createButton(text, disabled, onClick) {
	const button = document.createElement("button")
	button.type = "button"
	button.innerText = text
	button.disabled = disabled
	if (onClick) button.addEventListener("click", onClick)
	return button
}

export function createHTMXButton(id, text, disabled, onClick, hxAttributes) {
	const htmxButton = createButton(text, disabled, onClick)
	htmxButton.id = id
	htmxButton.type = "submit"
	Object.entries(hxAttributes).forEach(([key, value]) => htmxButton.setAttribute(key, value))
	return htmxButton
}

export function safeJSONParse(data) {
	if (!data) return null

	// If already an object/array, return as is
	if (typeof data !== "string") return data

	try {
		let parsed = data
		// First level parse
		while (typeof parsed === "string" &&
               (parsed.startsWith("[") || parsed.startsWith("{"))) {
			parsed = JSON.parse(parsed)
		}

		// Handle one level of nested JSON strings in object values
		if (typeof parsed === "object" && parsed !== null) {
			Object.keys(parsed).forEach(key => {
				if (typeof parsed[key] === "string") {
					try {
						if (parsed[key].startsWith("{") || parsed[key].startsWith("[")) {
							parsed[key] = JSON.parse(parsed[key])
						}
					} catch (e) {
						// Keep original value if parsing fails
						console.error("safeJSONParse parsing failed", e)
					}
				}
			})
		}

		return parsed
	} catch (error) {
		console.warn("JSON parse error:", error)
		return null
	}
}

export function safeStringify(obj) {
	const cache = new Set()
	return JSON.stringify(obj, (key, value) => {
		if (typeof value === "object" && value !== null) {
			if (cache.has(value)) {
				return "[Circular]" // Handle circular references
			}
			cache.add(value)
		}
		if (value instanceof Error) {
			return {
				message: value.message,
				name:    value.name,
				stack:   value.stack,
				...(value.code && { code: value.code }),
				...(value.level && { level: value.level }),
				...(value.details && { details: value.details }),
			}
		}
		return value
	})
}

export const visibilityStates = {
	initial: "hidden",
	states:  {
		hidden: {
			on: { SHOW: "visible" },
		},
		visible: {
			on: { HIDE: "hidden" },
		}
	}
}

export function cssTransition(element, machine, action) {
	if (!element) console.warn("Element not found in the DOM")
	const currentState = element.dataset.state || machine.initial
	const nextState = machine.states[currentState].on[action] || currentState
	element.dataset.state = nextState
}

export async function fetchData(url) {
	try {
		const response = await fetch(url)
		if (!response.ok) throw new Error("Network error")
		return await response.json()
	} catch (error) {
		console.error("Fetching error: ", error)
	}
}

// Add this function at the top of the file or before generateSearchBox
export function normalizeText(text) {
	return text
		.normalize("NFD") // Decompose characters into base + diacritics
		.replace(/[\u0300-\u036f]/g, "") // Remove diacritics
		.toLowerCase() // Convert to lowercase
		.trim() // Remove whitespace
}
