// router.js

import { updateState, getState } from "./states.js"
import { initializeHome } from "./components/home.js"
import { initializeFilters } from "./components/filters.js"
import { initializeStudy } from "./components/study.js"
import { initializeSimulation } from "./components/simulation.js"
import { initializeNotFound } from "./components/404.js"
import { initializeComponents, showConfirmationDialog } from "./components/components.js"
import { initializeAuth } from "./components/auth.js"
import { initializeResults } from "./components/results.js"
import { initializeLogout } from "./components/logout.js"
import { session } from "./session.js"

const componentsSource = "/src/web/components/"
const routes = {
	"/404":                { component: "404.html", initialize: initializeNotFound},
	"/auth":               { component: "auth.html", initialize: initializeAuth },
	"/components":         { component: "components.html", initialize: initializeComponents},

	"/logout":             { requiresAuth: true, component: "logout.html", initialize: initializeLogout },
	"/":                   { requiresAuth: true, component: "home.html", initialize: initializeHome },
	"/study/filters":      { requiresAuth: true, component: "filters.html", initialize: () => initializeFilters("study") },
	"/study/play":         { requiresAuth: true, component: "study.html", initialize: initializeStudy },
	"/study/results":      { requiresAuth: true, component: "results.html", initialize: () => initializeResults("study") },
	"/simulation/filters": { requiresAuth: true, component: "filters.html", initialize: () => initializeFilters("simulation") },
	"/simulation/play":    { requiresAuth: true, component: "simulation.html", initialize: initializeSimulation},
	"/simulation/results": { requiresAuth: true, component: "results.html", initialize: () => initializeResults("simulation") },
	"/results/:id":        { requiresAuth: true, component: "results.html", initialize: () => initializeResults("history") },

	// NOTE: Classroom is being handled using HTMX, so it's not being loaded here
	// "/classroom":         { component: "classroom.html", initialize: initializeClassroom },
}

const notFoundComponent = "<h2>404 - Page not found</h2>"

// matchRoute function handles both exact and pattern matching
// e.g /study/filters and /study/:id
function matchRoute(path) {
	// Check exact routes first
	if (routes[path]) return { route: routes[path], params: {} }

	// Split path into segments
	const pathSegments = path.split("/").filter(Boolean)

	// Check each route for pattern match
	for (const [routePath, routeConfig] of Object.entries(routes)) {
		const routeSegments = routePath.split("/").filter(Boolean)

		if (pathSegments.length !== routeSegments.length) continue

		const params = {}
		const isMatch = routeSegments.every((routeSeg, i) => {
			if (routeSeg.startsWith(":")) {
				// Store param value
				params[routeSeg.slice(1)] = pathSegments[i]
				return true
			}
			return routeSeg === pathSegments[i]
		})

		if (isMatch) {
			return { route: routeConfig, params }
		}
	}

	return { route: routes["/404"], params: {} }
}


async function fetchComponent(src, component) {
	const response = await fetch(src + component)
	if (!response.ok) throw new Error("Failed to fetch component")

	const content = await response.text()
	if (component !== "/" && content.includes("<body>")) {
		// Check if the content is index.html, if so, replace the body content
		throw new Error("Index page found")
	}
	return content
}

async function isBlockedByState() {
	const isBlocked = getState().navigationBlocked
	if (!isBlocked) return false

	const confirmed = await showConfirmationDialog("Si abandonas la página perderás tu progreso. ¿Deseas salir?", "Abandonar", "Cancelar")
	if (!confirmed) {
		return true
	}

	updateState({ navigationBlocked: false }) // reset flag
	return false
}

export async function loadComponent(fullPath) {
	const isBlocked = await isBlockedByState()
	if (isBlocked) return // Check if the user is trying to leave a page with unsaved data

	const [path, queryParams] = fullPath.split("?")
	let { route, params } = matchRoute(path)
	const mainContent = document.querySelector("#main-content")

	try {
		updateState({ currentRoute: path, queryParams: queryParams })
		history.pushState(null, null, fullPath) // fullPath includes query params

		if (route.requiresAuth && session.isStateTokenExpired()) {
			updateState({ isAuthenticated: false, user: {} })
			route = routes["/auth"]
		}

		mainContent.innerHTML = await fetchComponent(componentsSource, route.component)
		if (route.initialize && typeof route.initialize === "function") {
			route.initialize(getState().filters)

			window.scrollTo({
				top:      0,
				behavior: "smooth"
			})
		}
	} catch (error) {
		console.error("Error loading component:", error)
		mainContent.innerHTML = notFoundComponent
	}
}

// Listen for custom redirect events from 404.html
window.addEventListener("router-redirect", async (event) => {
	const { path, queryParams } = event.detail
	await loadComponent(path + queryParams) // Forward the full URL (path + query)
})
