
export function getSystemTheme() {
	return window.matchMedia("(prefers-color-scheme: dark)").matches ? "dark" : "light"
}

export const updateHTMLTagColor = (isDark) => {
	const htmlTag = document.querySelector("html")
	htmlTag.classList.add(`${isDark ? "bg-zinc-950" : "bg-gray-50"}`)
	htmlTag.classList.remove(`${!isDark ? "bg-zinc-950" : "bg-gray-50"}`)
}

export const toggleTheme = () => {
	const currentTheme = localStorage.theme || getSystemTheme()
	const newTheme = currentTheme === "dark" ? "light" : "dark"
	setTheme(newTheme)
}

export const setTheme = (theme) => {
	localStorage.theme = theme

	const isDark = theme === "dark"
	updateHTMLTagColor(isDark)
	document.documentElement.classList.toggle("dark", isDark)
}

export function toggleHiddenClass(selector, shouldHide) {
	document.querySelectorAll(selector).forEach(element => {
		element.classList.toggle("hidden", shouldHide)
	})
}
